/**
 * Container component for filters in the side bar. This same component can also be used as a search container
 * @param {String} id The id for the component
 * @param {String} title The title for the container
 * @param {Array} children The items in the container
 */
var React = require("react");
var _ = require("underscore");
var SecondaryButton = require('./SecondaryButton');

var SideBarFilterSubContainer = React.createClass({
    // styles
    sideBarFilterSubContainerStyle: {
        verticalAlign: "top",
        marginBottom: 20,
        fontFamily: "verdana",
        fontSize: 12
    },
    sideBarFilterSubContainerTitleStyle: {
        color: "#666666",
        fontWeight: "bold",
        marginBottom: 12,
        width: '50%',
        float: 'left',
        lineHeight: '21px'
    },
    sideBarFilterSubContainerButtonContainerStyle: {
        marginBottom: 12,
        width: '50%',
        float: 'right',
        textAlign: 'right'
    },
    
    render() {
        var id = this.props.id || _.uniqueId("sideBarFilterSubContainer_");

        // Width adjustment for title and button container
        if(this.props.clearButtonText && !this.props.title) {
            this.sideBarFilterSubContainerButtonContainerStyle.width = '100%';
            this.sideBarFilterSubContainerTitleStyle.width = '0%';
        } else if (!this.props.clearButtonText && this.props.title) {
            this.sideBarFilterSubContainerButtonContainerStyle.width = '0%';            
            this.sideBarFilterSubContainerTitleStyle.width = '100%';            
        } else {
            this.sideBarFilterSubContainerButtonContainerStyle.width = '40%';            
            this.sideBarFilterSubContainerTitleStyle.width = '60%';  
        }

        var handleClick = (evt) => {
            if (typeof this.props.clearButtonClickHandler === 'function') {
                this.props.clearButtonClickHandler(evt);
            }
        };
        var getButtonContainer = () => {
            if (this.props.clearButtonText && this.props.clearButtonText.length > 0) {
                return (
                    <div style={this.sideBarFilterSubContainerButtonContainerStyle}>
                        <SecondaryButton id='clearButton' text={this.props.clearButtonText} clickHandler={handleClick}/>
                    </div>
                );
            }
        }
        var getTitle = () => {
            if (this.props.title) {
                return (
                    <div style={this.sideBarFilterSubContainerTitleStyle} data-component-name='SideBarFilterSubContainer'
                        data-component-type='Label'>{this.props.title}</div>
                );
            } 
            return null;
        };
        return (
            <div id={id} style={this.sideBarFilterSubContainerStyle} data-component-name='SideBarFilterSubContainer'
                data-component-type='Container'>
                <div>
                    {getTitle()}
                    {getButtonContainer()}
                    <div style={{clear: 'both'}}></div>
                </div>
                {this.props.children}
            </div>
        );
    }
});


module.exports = SideBarFilterSubContainer;


