var React = require("react");

var tableHeaderRowStyle = {
    border: '1px solid #E1E1E1',
    borderCollapse: 'collapse',
    padding: 5,
    fontWeight: 'bold',
    fontSize: 12,
    color: '#333333',
    verticalAlign: 'middle',
    borderBottom: '1px solid #E1E1E1',
    textAlign: 'left',
    whiteSpace: 'nowrap',
    paddingTop: 8,
    paddingRight: 5,
    paddingBottom: 8,
    paddingLeft: 5,
    height: 20
};
var RateTableHeader = React.createClass({
    render () {
        var headers = this.props.headers;
        return <thead>
        <tr>
            {headers.map(function(result) {
              var tableHeaderKey = _.uniqueId("rateTableHeader_");
                return <th key={tableHeaderKey} style={tableHeaderRowStyle}>{result}</th>
            })
            }
        </tr>
        </thead>
    }
});

module.exports = RateTableHeader;



