// A Balloon styled component (with an up arrow) that takes in a size and an arrowLocation properties
const React = require("react");
const PropTypes  = require("prop-types");
const styled = require('styled-components').default;

const Balloon = styled.div`
    width: ${props => props.size + "px"};
    border: 2px solid #E1E1E1;
    border-radius: 5px;
    padding-top: 8px;
    padding-bottom: 6px;
    position: relative;
    background: white;

    &:after, &:before {
	    border-left: 8px solid transparent;
	    border-right: 8px solid transparent;
	    border-bottom: 8px solid white;
	    top: -8px;
	    content: '';
	    margin-left: ${props => {
    		if (props.arrowLocation == "left")
    			return "30px";
    		else if (props.arrowLocation == "center")
    			return (props.size/2-5) + "px";
    		else if (props.arrowLocation == "right")
    			return (props.size-32) + "px";
    		else if (props.arrowLocation >= 0)
    			return props.arrowLocation + "px";
    		else 
    			return (props.size+props.arrowLocation) + "px";
	    	}
	    };
	    position: absolute;
  	}

	&:before {
	    border-left: 11px solid transparent;
	    border-right: 11px solid transparent;
	    border-bottom: 11px solid;
	    border-bottom-color: #E1E1E1; 
	    top: -11px;
	    margin-left: ${props => {
    		if (props.arrowLocation == "left")
    			return "28px";
    		else if (props.arrowLocation == "center")
    			return (props.size/2-7) + "px";
    		else if (props.arrowLocation == "right")
    			return (props.size-34) + "px";
    		else if (props.arrowLocation >= 0)
    			return (props.arrowLocation-2) + "px";
    		else 
    			return (props.size+props.arrowLocation-2) + "px";
	    	}
	    };
	}  	
`;

class BalloonContainer extends React.Component {

    render () {
    	if (this.props.show)
	        return (
	            <Balloon id={this.props.id} size={this.props.size || 200} arrowLocation={this.props.arrowLocation || "center"}>
	                {this.props.children}
	            </Balloon>
	        )
    	else 
    		return null;
    }
}

BalloonContainer.propTypes = {
    /**
     * id
     */
	id: PropTypes.string,
    /**
     * Width of the container
     */
	size: PropTypes.number,
    /**
     * Where to place the arrowhead? Can be one the 3 pre-defined locations or a number:
     * a positive number denotes the number of pixels from the left edge of the container, while a negative number denotes the number of pixels from the right edge of the container
     */
	arrowLocation: PropTypes.oneOfType([
		PropTypes.oneOf(['left', 'center', 'right']),
		PropTypes.number
	])
}

module.exports = BalloonContainer;



