/**
 * Row in the Rate table component
 */
var React = require("react");
var TextBox = require("./TextBox");
var SecondaryButton = require("./SecondaryButton");

var tableHeaderRowStyle = {
    border: '1px solid #E1E1E1',
    borderCollapse: 'collapse'
};

var showDeleteRowStyle = {
    float: 'right',
    margin: 6
};

var hideDeleteRowStyle = {
    display: 'none'
};

var deleteButtonStyle = {
    color: "#CCCCCC",
    cursor: "pointer",
    'fontSize': '12px'
};

var RateTableRow = React.createClass({
    // custom methods
    // lifecycle event handlers
    /**
     * We need to change rows when the rows props change
     */
    componentWillReceiveProps:function (newProps) {
        var rows = newProps.rows;
        this.setState({ rows: rows });
    },
    getInitialState:function () {
        var rows = this.props.rows;
        return {
            rows: rows // value set by prop because it is the default value
        };
    },

    handleChange (index, count) {
        this.props.rows.splice(index, count);
        this.props.onDeleteRow(this.props.rows);
    },

    render () {
        var self = this;
        var results = this.props.rows;
        var validate = this.props.onValueChange;
        var requiredRows = this.props.requiredRows || 1; // 1 row is always required unless set to 0
        var index = -1;
        var callback = (results) => {
          this.props.updateState(results);
        };
        var handleRows = (results, index) => {
            return results.map((result) => {
                index+= 1;

                var tableRowKey = _.uniqueId("rateTableRow_");
                var keys = Object.keys(result);
                var keysLength = keys.length;
                var keyIndex = -1;
                var handleColumns = (result) => {
                    return keys.map((key) => {
                        var tableRowTDKey = _.uniqueId("rateTableRowTD_");
                        var tableRowTBKey = _.uniqueId("rateTableTBRow_");
                        var tableRowDBKey = _.uniqueId("rateTableDBRow_");
                        keyIndex+= 1;
                        var deleteButton = (keyIndex) => { return keyIndex === keysLength - 1 ?
                            <div key={tableRowDBKey} data-index={index} style={index < requiredRows ? hideDeleteRowStyle : showDeleteRowStyle}>
                                <SecondaryButton icon="fa fa-fw fa-times-circle-o"
                                    clickHandler={
                                        (event, index) => {
                                            self.handleChange($(event.currentTarget.parentElement).data('index'), 1);
                                        }
                                    }
                                />
                            </div> : ''};
                        return (
                            <td key={tableRowTDKey} data-rangeindex={index} style={tableHeaderRowStyle}>
                                <TextBox key={tableRowTBKey} disabled={false} width={145} value={result[key]} focusLostHandler={(event) => {validate(event.target.value, event, key, result, results, callback)}} />
                                {deleteButton(keyIndex)}
                            </td>
                        );
                    })
                };
                return <tr key={tableRowKey} className="rangeRow">
                    {handleColumns(result)}
                </tr>
            })
        };

        return <tbody id="rangeList">
        {handleRows(results, index)}
        </tbody>

    }
});

module.exports = RateTableRow;



