var React = require("react");
var _ = require("underscore");
var SecondaryButton = require("./SecondaryButton");
var PrimaryButton = require("./PrimaryButton");
var CheckBox = require("./CheckBox");
var CheckBox = require("./CheckBox");
var Table = require("./Table");
var TableHeadRow = require("./TableHeadRow");
var TableHeadColumn = require("./TableHeadColumn");
var TableNormalRow = require("./TableNormalRow");
var TableTextColumn = require("./TableTextColumn");
var PageFooter = require("./PageFooter");
var FileUpload = require("./FileUpload");

/**
 * Dialog used for uploading files. 
 */
var UploadDialog = React.createClass({
    containerStyle: {
        backgroundColor: "#ffffff"
    },
    dialogBackdrop: {
        backgroundColor: "#555",
        position: "fixed",
        top: 0,
        right: 0,
        bottom: 0,
        left: 0,
        zIndex: 1040,
        opacity: 0.8,
        display: "block"
    },
    modal: {
        backgroundColor: "#fff",
        fontSize: "12px",
        width: 940,
        position: "fixed",
        left: "50%", // need this to center
        top: "10%",
        marginLeft: -470, // half the width, to center the div
        marginRight: "auto",
        zIndex: 1050,    
        border: "1px solid #CFCFCF",
        outline: "none"
    },
    header: {
        padding: "21px 20px",
        lineHeight: "22px",
    },
    headerTitle: {
        fontFamily: "verdana",
        fontWeight: "bold",
        fontSize: 18,
        color: "#333333"
    },
    headerClose: {
        float: "right",
        color: "#CACBCA",
        fontSize: 18,
        top: 2,
        position: "relative",   
        cursor: "pointer",
    },
    body: {
        borderTop: "1px solid #E1E1E1",
        borderBottom: "1px solid #E1E1E1",
        padding: "32px 20px 32px 20px",   
        fontFamily: "verdana",
        fontSize: 14,
        color: "#333333",
    },
    footer: {
        padding: "16px 20px",
        backgroundColor: "#f5f5f5",
        textAlign: "right",
    },
    subTitleContainerStyle: {
        marginBottom: 24,
        fontFamily: "verdana",
        fontSize: 14,
        color: "#333"
    },
    overwriteMessageContainerStyle: {
        marginBottom: 24,
        fontFamily: "verdana",
        fontSize: 14,
        color: "#333",
        marginLeft: 20
    },
    importantMessageContainerStyle: {
        fontStyle: "italic",
        fontFamily: "verdana",
        fontSize: 14,
        color: "#333",
        marginLeft: 20
    },
    importantLabelStyle: {
        fontWeight: "bold"
    },
    inputFileStyle: {
        fontFamily: "verdana",
        fontSize: 14,
        color: "#333",
        lineHeight: "normal"
    },
    inputFileContainerStyle: {
        marginBottom: 24,
        marginLeft: 55,
    },
    errorSectionContainerStyle: {
        marginTop: 32
    },
    errorHeadingStyle: {
        fontFamily: "verdana",
        fontSize: 14,
        color: "#333",
        fontWeight: "bold",
        float: "left"
    },
    errorLogFileLinkStyle: {
        fontFamily: "verdana",
        fontSize: 14,
        color: "#346F8F",
        float: "right",
        cursor: "pointer"
    },
    errorTableContainerStyle: {
        height: 128,
        overflowX: "auto",
        marginTop: 12
    },
    // styles
    // properties
    // properties
    // lifecycle event handlers
    propTypes: {
        /**
         * Toggle to show or hide the overlay. False by default.
         */
        show: React.PropTypes.bool,
        /**
         * Determines if the dialog is modal. Defaults to true.
         */
        isModal: React.PropTypes.bool,
        /**
         * The text for the download error log button.
         */
        downloadErrorLogText: React.PropTypes.string,
        /**
         * The title for the error table.
         */
        errorSectionTitle: React.PropTypes.string,
        /**
         * The text for the Primary button.
         */
        primaryButtonText: React.PropTypes.string,
        /**
         * The text for the cancel button.
         */
        cancelButtonText: React.PropTypes.string,
        /**
         * Title of the Overlay.
         */
        title: React.PropTypes.string,
        /**
         * This should be set to the error response returned by the upload service
         * from XDM when there is an error uploading the file.
         * The format of the errors array JSON should match the following:
         * [
                {
                    "rowIndex": 2,
                    "fieldName": "T1: QA",
                    "errorMessage": "Required field T1: QA. Enter a value."
                },
                {
                    "rowIndex": 2,
                    "fieldName": "T1: PT",
                    "errorMessage": "Required field T1: PT. Enter a value."
                }
            ]
         */
        errors: React.PropTypes.array,
        /**
         * Handler to be passed into the component. It is called when the upload button is clicked.
         */
        uploadClickHandler: React.PropTypes.func,
        /**
         * Handler called when the upload dialog cancel button is clicked.
         */
        cancelClickHandler: React.PropTypes.func,
        /**
         * Function to assign to the prop for downloading the error log file.
         */
        downloadErrorLogClickHandler: React.PropTypes.func,
        /**
         * The text to display inside the dialog as the first sentence.
         */
        subTitle: React.PropTypes.string,
        /**
         * Message to display next to the checkbox for overwriting upload. If this text is not supplied
         * the checkbox is not displayed.
         */
        overwriteCheckboxMessage: React.PropTypes.string,
        /**
         * Message shown in italics preceded with the label "Important:" 
         */
        importantMessage: React.PropTypes.string,
        /**
         * The prop value for the word 'Important'. The ':' is added automatically.
         */
        importantLabelText: React.PropTypes.string,
        /**
         * The text to display on the file upload button.
         */
        fileUploadButtonText: React.PropTypes.string,
        /**
         * The text to display in the file upload input box as a placeholder.
         */
        fileUploadPlaceholderText: React.PropTypes.string,
        /**
         * The id of the primary button on the confirmation dialog. If not provided a unique id will be used.
         */
        primaryButtonId: React.PropTypes.string,
        /**
         * The id of the cancel button on the confirmation dialog. If not provided a unique id will be used.
         */
        cancelButtonId: React.PropTypes.string,
        /**
         * Handler to be passed in to get notified when the overwrite checkbox is checked or unchecked.
         */
        overwriteCheckboxChangeHandler: React.PropTypes.func,
        /**
         * Custom params to use intead of the default ones.
         */
        acceptParams: React.PropTypes.string
    },
    getInitialState () {
        var showOverlay = false;
        if (typeof this.props.show == "boolean") {
            showOverlay = this.props.show;
        }
        return {
            show: showOverlay,
            uploadButtonEnabled: false,
            files: null
        };
    },
    componentWillReceiveProps (newProps) {
        if (typeof newProps.show == "boolean") {
            this.setState({
                show: newProps.show
            });
        }
    },
    render () {
        var id = this.props.id || _.uniqueId("uploadDialog_");
        var primaryButtonText = this.props.primaryButtonText || "[Ok]";
        var primaryButtonId = this.props.primaryButtonId || _.uniqueId("confirmationDialogPrimaryButton_");
        var cancelButtonId = this.props.cancelButtonId || _.uniqueId("confirmationDialogCancelButton_");
        var title = this.props.title || "[Title]";
        var errorSectionTitle = this.props.errorSectionTitle || "[Error Uploading File]";
        var downloadErrorLogText = this.props.downloadErrorLogText || "[Download error log]";
        // var fileUploadButtonText = this.props.fileUploadButtonText || "[Browse...]";
        var fileUploadPlaceholderText = this.props.fileUploadPlaceholderText || "[Select A File]";

        var renderSubTitle = () => {
            if (this.props.subTitle && this.props.subTitle.length > 0) {
                return (
                    <div style={this.subTitleContainerStyle}>{this.props.subTitle}</div>
                );
            } else {
                return null;
            }
        };
        var renderOverwriteCheckbox = () => {
            if (this.props.overwriteCheckboxMessage && this.props.overwriteCheckboxMessage.length > 0) {
                return (
                    <div style={this.overwriteMessageContainerStyle}>
                        <CheckBox id="overwriteCheckBox" text={this.props.overwriteCheckboxMessage} changeHandler={this.props.overwriteCheckboxChangeHandler}/>
                    </div>
                );
            } else {
                return null;
            }
        };
        var renderSteps = () => {
            var totalSteps = React.Children.count(this.props.children);
            return React.Children.map(this.props.children, (child, index, arr) => {
                return React.cloneElement(child, {
                    childIndex: index,
                    totalSteps: totalSteps
                });
            });
        };
        var renderImportantMessage = () => {
            if (this.props.importantMessage && this.props.importantMessage.length > 0) {
                var importantLabelText = (this.props.importantLabelText || "[Important]") + ": ";
                return (
                    <div style={this.importantMessageContainerStyle}>
                        <span style={this.importantLabelStyle}>{importantLabelText}</span><span>{this.props.importantMessage}</span>
                    </div>
                );
            } else {
                return null;
            }
        };
        /**
         * Function called when the download error log button is clicked.
         * It will call the passed in prop function 'downloadErrorLogClickHandler'
         */
        var downloadErrorLog = (event) => {
            if (typeof this.props.downloadErrorLogClickHandler == "function") {
                this.props.downloadErrorLogClickHandler();
            }
        };

        var renderErrorInformation = () => {
            if (this.props.errors && this.props.errors.length > 0) {
                return (
                    <div style={this.errorSectionContainerStyle}>
                        <div style={{width: "100%"}}>
                            <div style={this.errorHeadingStyle}>{errorSectionTitle}</div>
                            <div style={this.errorLogFileLinkStyle} onClick={downloadErrorLog}>{downloadErrorLogText}</div>
                            <div style={{clear: "both"}}></div>
                        </div>
                        <div style={this.errorTableContainerStyle}>
                            <Table id="errorTable" data={this.props.errors}>
                                <TableHeadRow>
                                    <TableHeadColumn columnName="Row Index"></TableHeadColumn>
                                    <TableHeadColumn columnName="Field"></TableHeadColumn>
                                    <TableHeadColumn columnName="Message"></TableHeadColumn>
                                </TableHeadRow>
                                <TableNormalRow>
                                    <TableTextColumn textPropertyName="rowIndex"></TableTextColumn>
                                    <TableTextColumn textPropertyName="fieldName"></TableTextColumn>
                                    <TableTextColumn textPropertyName="errorMessage"></TableTextColumn>
                                </TableNormalRow>
                            </Table>
                        </div>
                    </div>
                );
            } else {
                return null;
            }
        };

        var fileChanged = (files, event) => {
            this.setState({
                uploadButtonEnabled: true,
                files: files
            });
        };
        var renderFileInput = () => {
            if (this.props.fileUploadButtonText && this.props.fileUploadButtonText.length > 0) {
            	var acceptParams = this.props.acceptParams || ".csv,.xls,.xlsx,application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet";
                return (
                    <div style={this.inputFileContainerStyle}>
                        <FileUpload id={this.props.id} buttonText={this.props.fileUploadButtonText} placeholder={fileUploadPlaceholderText}
                            acceptParams={acceptParams}
                            changeEventHandler={fileChanged} tabIndex={2}/>
                    </div>
                );
            } else {
                return null;
            }
        };

        var cancelClickHandler = () => {
            this.setState({
                show: false
            }, () => {
                if (typeof this.props.cancelClickHandler == "function") {
                    this.props.cancelClickHandler();
                }
            });
            event.stopPropagation();
        };
        var uploadClickHandler = () => {
            if (typeof this.props.uploadClickHandler == "function") {
                this.props.uploadClickHandler(this.state.files);
            }
            event.stopPropagation();
        };
        var renderDialogBackdrop = () => {
            if (this.props.isModal == false) {
                return null;
            } else {
                return (
                    <div style={this.dialogBackdrop}></div>
                );
            }
        };
        var getModalStyle = () => {
            if (this.props.isModal == false) {
                return null;
            } else {
                return this.modal;
            }
        };
        var renderHeader = () => {
            if (this.props.isModal == false) {
                return null;
            } else {
                return (
                    <div style={this.header}>
                        <i className="fa fa-times" style={this.headerClose}  onClick={cancelClickHandler}></i>
                        <div style={this.headerTitle}>
                            {title}
                        </div>
                    </div>
                );
            }
        };
        var renderFooter = () => {
            if (this.props.isModal == false) {
                return null;
            } else {
                return (
                    <PageFooter>
                        <PageFooter.RightPane>
                            <SecondaryButton id={cancelButtonId} text={this.props.cancelButtonText || "[Cancel]"} 
                                icon="fa fa-times-circle-o" clickHandler={cancelClickHandler}/>
                            <PrimaryButton id={primaryButtonId} inline={true} enabled={this.state.uploadButtonEnabled}
                                text={primaryButtonText} clickHandler={uploadClickHandler}/>
                        </PageFooter.RightPane>
                    </PageFooter>
                );
            }
        };
        if (this.state.show == false) {
            return null;
        } else {
            return (
                <div id={id} style={this.containerStyle}>
                    {renderDialogBackdrop()}
                    <div style={getModalStyle()}>
                        {renderHeader()}
                        <div style={this.body}>
                            {renderSubTitle()}
                            {renderSteps()}
                            {renderFileInput()}
                            {renderOverwriteCheckbox()}
                            {renderImportantMessage()}
                            {renderErrorInformation()}
                        </div>
                        {renderFooter()}
                    </div>
                </div>
            );
        }
    }
    // lifecycle event handlers
});

module.exports = UploadDialog;


