var React = require("react");
var SearchBoxStyles = require("./ComponentStyles").searchBox;
var TextBox = require("./TextBox");

var DateControlInput = React.createClass({
    displayName: 'DateControlInput',

    propTypes: {
        onClick: React.PropTypes.func,
        value: React.PropTypes.string
    },

    render () {
        var hideTextBox = true;
        if (typeof this.props.hideTextBox == "boolean") {
            hideTextBox = this.props.hideTextBox;
        }
        var getTextBox = () => {
            if (hideTextBox == true) {
                return null;
            } else {
                var readOnlyDateContainerStyle ={};                
                if (this.props.inputDisabled === true) {
                    var readOnlyDateContainerStyle = {
                        alignItems: "center",
                        backgroundColor: "#fff",
                        backgroundImage: "linear-gradient(-180deg, #FFFFFF 0%, #F3F3F3 100%)",
                        border: "solid 1px #E3E3E3",
                        borderRadius: "2px 0px 0px 2px",
                        boxSizing: "border-box",
                        cursor: "pointer",
                        display: "flex",
                        float: "left",
                        fontFamily: "verdana",
                        fontSize: 12,
                        height: 28,
                        MozBoxSizing: "border-box",
                        padding: "0px 8px 0px 8px",
                        position: "relative",
                        width: 172
                    };
                    if (this.props.isRequired === true) {
                        readOnlyDateContainerStyle.borderLeft = "solid 3px #F7901E";
                    }
                    return (
                        <div style={readOnlyDateContainerStyle}>
                            {this.props.value}
                        </div>
                    );
                } else {
                	let dateContainerStyle = SearchBoxStyles.inputContainerStyle;
                    if (this.props.isRequired === true) {
                    	dateContainerStyle = Object.assign({},dateContainerStyle,{borderLeft: "solid 3px #F7901E"});
                    }
                    return (
                        <div style={dateContainerStyle}>
                            <TextBox id={"textbox_" + id} disabled={this.props.disabled} changeHandler={this.props.dateInputHandler}
                                value={this.props.value} validator={this.props.validator} tabIndex={this.props.tabIndexx}
                                standalone={false} type="dateControl" />
                        </div>
                    );
                }
            }
        }

        var getIconStyle = () => {
            var iconStyle = SearchBoxStyles.searchButtonContainerStyle;
            if (hideTextBox == true) {
                iconStyle = SearchBoxStyles.searchButtonStandaloneContainerStyle;
            }
            if (this.props.disabled === true) {
                iconStyle.backgroundColor = "#F3F3F3";
                iconStyle.backgroundImage = "none";
                iconStyle.cursor = "default";
            } else {
                iconStyle.backgroundColor = "none";
                iconStyle.backgroundImage = "linear-gradient(-180deg, #FFFFFF 0%, #F3F3F3 100%)";
                iconStyle.cursor = "pointer";
            }
            return iconStyle;
        };
        var id = this.props.id || _.uniqueId("dateControlInput_");
        return (
            <div id={id} onClick={this.props.onClick}>
                {getTextBox()}
                <div id={"button_" + id} style={getIconStyle()}>
                    <i className="fa fa-calendar"></i>
                </div>
                <div style={{clear: "both"}}></div>
            </div>  
        );
    }
});

module.exports = DateControlInput;


