var React = require("react");
var RateTableHeader = require("./RateTableHeader");
var RateTableRow = require("./RateTableRow");
var SecondaryButton = require("./SecondaryButton");
var _ = require("underscore");

var tableStyle = {
    width: '100%',
    margin: '0',
    display: 'inline-block'
};
var divStyle = {
    display: 'inline-block',
    verticalAlign: 'top'
};
var secondaryButtonStyle = {
    'margin-top':'5px'
};

var RateTable = React.createClass({
    componentWillReceiveProps:function (newProps) {
        var rangeList = newProps.rangeList;
        this.setState({ rangeList: rangeList });
    },
    getInitialState:function () {
        var rangeList = this.props.rangeList;
        return {
            rangeList: rangeList // value set by prop because it is the default value
        };
    },
    handleClick () {
        var rangeList = this.state.rangeList || [];
            rangeList.push({
                rangeLow: 0,
                rangeHigh: 0,
                rangeValue: 0
            });
            this.setState({ rangeList: rangeList });
    },

    updateState (newState) {
        this.setState({rangeList: newState});
        this.props.updateRowStyle();
    },

    render () {
        var key = _.uniqueId("rateTable_");
        var results = this.state.rangeList;
        var headers = this.props.headers;
        var addRow = this.props.addRow;
        var id = this.props.id || _.uniqueId("rateTable_");
        return (
            <div style={divStyle}>
                <table style={tableStyle} id={id} key={key}>
                    <RateTableHeader headers={headers} />
                    <RateTableRow rows={results} updateState={this.updateState} onDeleteRow={this.updateState} onValueChange={this.props.validate}/>
                </table>
                <SecondaryButton style={secondaryButtonStyle} icon="fa fa-plus-circle" text={addRow} clickHandler={this.handleClick}/>
            </div>
        );
    }
});

module.exports = RateTable;



