/**
 * Overlay component.
 * @param {Boolean} show display propertye, false by detault.
 * @param {String} type Type of the overlay it supports types confirm, save and delete. "confirm" type is the default one. 
 * @param {String} headerText Title of the Overlay.
 * @param {String} bodyText Body text.
 * @param {String} footerButtonText Footer button text.
 * @param {String} footerSecondayButtonText Secondary footer button text in case of "save" type.
 * @param {Function} confirmClickHandler Handler to be passed into the component. It is called when the cofirm button is clicked.
 * @param {Function} cancelClickHandler Handler to be passed into the component that is called when the user clicks the 'x' icon or cancel link to close the overlay.
 * @param {Function} secondaryButtonClickHandler Handler to be passed into the component. It is called when the secondary footer button is clicked.
 */
import React, { Component } from 'react';
export default class Overlay extends Component {
    constructor(props) {
        super(props);
        this.confirmClickHandler = this.confirmClickHandler.bind(this);
        this.cancelClickHandler = this.cancelClickHandler.bind(this);
        this.secondaryButtonClickHandler = this.secondaryButtonClickHandler.bind(this);
        
        //style
        this.dialogBackdrop = {
            backgroundColor: "#555",
            position: "fixed",
            top: 0,
            right: 0,
            bottom: 0,
            left: 0,
            zIndex: 1040,
            opacity: 0.8,
            display: "block"
        }
        this.modal  = {
            backgroundColor: "#fff",
            fontSize: "12px",
            width: 610,
            position: "fixed",
            left: "calc(50% - 305px)",
            top: "10%",
            marginLeft: "auto",
            marginRight: "auto",
            zIndex: 1050,    
            border: "1px solid #CFCFCF",
            outline: "none",

        }
        this.header = {
            padding: "21px 20px",
            lineHeight: "22px",
        }
        this.headerTitle = {
            fontFamily: "verdana",
            fontWeight: "bold",
            fontSize: 18,
            color: "#333333"
        }
        this.headerClose = {
            float: "right",
            color: "#CACBCA",
            fontSize: 18,
            top: 2,
            position: "relative",   
            cursor: "pointer",
        }
        this.body = {
            borderTop: "1px solid #E1E1E1",
            borderBottom: "1px solid #E1E1E1",
            padding: "28px 40px",   
            fontFamily: "verdana",
            fontSize: 14,
            color: "#333333",
        }
        this.footer = {
            padding: "16px 20px",
            backgroundColor: "#f5f5f5",
            textAlign: "right",
        }
        this.footerButton = {
            borderRadius: 15,
            border: "1px solid #D3D4D6",
            background: "linear-gradient(top, #fff, #ebf1f5); background: -webkit-linear-gradient(top, #fff, #ebf1f5); background: -moz-linear-gradient(top, #fff, #ebf1f5); background: -ms-linear-gradient(top, #fff, #ebf1f5); background: linear-gradient(top, #fff, #ebf1f5)", 
            color: "#285c7e",
            textShadow: "0px 0px 0 rgba(0,0,0,0.3)",
            boxShadow: "#9DA1A4 0 1px 2px",
            fontFamily: "Verdana",
            fontWeight: "bold",
            textAlign: "center",
            fontSize:12,
            padding: "5px 20px",
            outline: "none"

        }
        this.footerButtonSecondary = Object.assign({}, this.footerButton);
        this.footerButtonSecondary.float = "left";
        this.footerCancelButton = {
            background: "transparent",
            border: "none",
            fontFamily: "Verdana",
            color: "#346f8f",
            marginRight: 10,
            marginTop: 6,   
            fontSize:12,
            outline: "none" 
        }
        this.footerCancelIcon = {
            cursor: "pointer",
            paddingRight: 3
        }
        
        //Default Text defintions.
        this.headerText = props.headerText || (props.type === "confirm" ? "Confirm" : props.type === "save" ? "Save" : "Error");
        this.bodyText = props.bodyText || (props.type === "confirm" ? "Do you want to delete?" : props.type === "save" ? "Your changes will be lost if you don't save them" : 'Error Meessage');
        this.footerButtonText = props.footerButtonText || (props.type === "confirm" ? "Ok" : props.type === "save" ? "Save" : "Ok");
        this.footerSecondayButtonText = props.footerSecondayButtonText || "Don't Save";
        
    }

    //custom methods
    confirmClickHandler () {
        console.log("confirmClickHandler:");
        if (typeof this.props.confirmClickHandler == "function") {
            this.props.confirmClickHandler();
        } else {
            event.stopPropagation();
        }
    }    
    cancelClickHandler () {
        console.log("cancelClickHandler:");
        if (typeof this.props.cancelClickHandler == "function") {
            this.props.cancelClickHandler();
        } else {
            event.stopPropagation();
        }
    }    
    secondaryButtonClickHandler () {
        console.log("secondaryButtonClickHandler:");
        if (typeof this.props.secondaryButtonClickHandler == "function") {
            this.props.secondaryButtonClickHandler();
        } else {
            event.stopPropagation();
        }
    }    
    renderFooterSecondaryButton() {
        if (this.props.type === "save") {
            return <button style={this.footerButtonSecondary} onClick={this.secondaryButtonClickHandler}>{this.footerSecondayButtonText} </button>
        }
        return null;
    }
	render() {
        if (!this.props.show) {
            return null;
        }
	    return  <div>
                    <div style={this.dialogBackdrop}></div>
                    <div style={this.modal}>
                        <div style={this.header}>
                            <i className="fa fa-times" style={this.headerClose}  onClick={this.cancelClickHandler}></i>
                            <div style={this.headerTitle}>
                                {this.headerText}
                            </div>
                        </div>
                        <div style={this.body}>
                            {this.bodyText}
                        </div>
                        <div style={this.footer}>
                            <button style={this.footerCancelButton} onClick={this.cancelClickHandler}>                        
                                    <i className="fa fa-times-circle-o" style={this.footerCancelIcon}></i>                        
                                    <span>Cancel1234</span>                    
                            </button>                        
                            <button style={this.footerButton} onClick={this.confirmClickHandler}>{this.footerButtonText} </button>
                            {this.renderFooterSecondaryButton()} 
                        </div>
                    </div>
                </div>

    }
}  

Overlay.propTypes = {
    show                     : React.PropTypes.bool.isRequired,
    type                     : React.PropTypes.oneOf(['confirm', 'save', 'delete']),
    headerText               : React.PropTypes.string,
    bodyText                 : React.PropTypes.string,
    footerButtonText         : React.PropTypes.string,
    footerSecondayButtonText : React.PropTypes.string,    
}
Overlay.defaultProps = {
    show    : false,
    type    : "confirm"
}


