// Sample component that highlights how to show an icon
var React = require("react");
var AbstractTableColumn = require("./AbstractTableColumn");

var TableStatusColumn = React.createClass({
    render () {
        var rowData = this.props.rowData;
        var propertyName = this.props.statusPropertyName;
        var getStatus = () => {
            var status = null;
            switch (rowData[propertyName]) {
                case "inprogress":
                    status = (
                        <span className="fa fa-spinner fa-spin" style={{color: "#333", fontSize: 12}}></span>  
                    );
                    break;
                case "paused":
                    status = (
                        <span className="fa-stack fa-1x" style={{top: -8, left: -5}}>
                            <i className="fa fa-circle fa-stack-1x" style={{color: "#3E6D91", fontSize: 14}}></i>
                            <i className="fa fa-pause fa-stack-1x" style={{color: "#fff", fontSize: 6}}></i>
                        </span>
                    );
                    break;
                case "failed":
                case "FAILED":
                    status = (
                        <span className="fa fa-exclamation-circle" style={{color: "#F79024", fontSize: 14}}></span>  
                    );
                    break;
            }
            return status;
        };
        return (
            <AbstractTableColumn isExpanded={this.props.isExpanded}>
                {getStatus()}
            </AbstractTableColumn>
        );
        // return (
        //     React.cloneElement(AbstractTableColumn, this.props, status)
        // );
    }
});

module.exports = TableStatusColumn;


