var React = require("react");
var _ = require("underscore");

/**
 * Button used in the input table, search box components that is used for remove/cancel actions.
 */
var RemoveRowButton = React.createClass({
    // custom properties
    buttonEnabledStyle () {
        return {
            cursor: "default",
            color: "#CCCCCC",
            fontSize: 15
        };
    },
    buttonEnabledHoverStyle () {
        return {
            cursor: "default",
            color: "#999999",
            fontSize: 15
        };
    },
    innerLayerStyle: {
        display: "table-cell", // use this style so that we can vertically middle align the content in the button
        verticalAlign: "middle",
        height: 22,
        cursor: 'pointer'
    },
    // custome properties
    // custom methods
    // custom methods
    // lifecycle event handlers
    propTypes: {
        /**
         * Id for the button, if not provided a unique id is added.
         */
        id: React.PropTypes.string,
        /**
         * Handler for the click event of the button.
         */
        clickHandler: React.PropTypes.func
    },
    getInitialState () {
        return {
            showHover: false
        };
    },
    render () {
        /**
         * Handler function for MouseEnter event
         * @param {Object} The event object from the MouseEnter event
         */
        var showHover = (event) => {
            this.setState({ showHover: true });
            event.stopPropagation();
        };
        /**
         * Handler function for MouseLeave event
         * @param {Object} The event object from the MouseLeave event
         */
        var hideHover = (event) => {
            this.setState({ showHover: false });
            event.stopPropagation();
        };

        var clickHandler = (event) => {
            if (typeof this.props.clickHandler == "function") {
                this.props.clickHandler(this.props.id);
            }
        };
        var currentStyle = () => {
            var buttonStyle = {};
            if (this.state.showHover == true) {
                buttonStyle =  this.buttonEnabledHoverStyle();
            } else {
                buttonStyle = this.buttonEnabledStyle();
            }
            return buttonStyle;
        };
        
        var id = this.props.id || _.uniqueId("removeRowButton_");
        return (
            <div id={id} style={currentStyle()} onMouseEnter={showHover} onMouseLeave={hideHover}>
                <div style={this.innerLayerStyle} onClick={clickHandler}>
                    <span className="fa fa-times-circle"></span>
                </div>
            </div>  
        );
    }
    // lifecycle event handlers
});

module.exports = RemoveRowButton;


