/**
 * Sub component inside the InputTable that displays a row of data
 */
var React = require("react");
var _ = require("underscore");
var InputTableLabelColumn = require("./InputTableLabelColumn");
var InputTableRemoveRowButtonColumn = require("./InputTableRemoveRowButtonColumn");

var InputTableDataRow = React.createClass({
    // styles
    rowStyle: {
        backgroundColor: "#FFF",
    },
    // styles
    // custom methods
    // custom methods
    // lifecycle event handlers
    render () {
        var requiredRows = 1; // default number of required rows
        if (typeof this.props.requiredRows == "number" && this.props.requiredRows > 1) {
            requiredRows = this.props.requiredRows;
        }
        var id = ((typeof this.props.dataIndex == "number") ? "inputTableDataRow" + this.props.dataIndex : _.uniqueId("inputTableDataRowId_"));
        var data = this.props.data;
        var totalRows = this.props.totalDataItems;
        var totalTierRows = totalRows - 1;// minus threshold row
        var requiredTierRows = requiredRows - 1; // minus threshold row
        var renderColumns = () => {
            // use the index to add a unique key to each element in the map, otherwise
            // react throws a warning about missing unique 'key' prop for each child
            // in an array or iterator
            var index = -1;
            var renderedColumns = React.Children.map(this.props.children, (child, childIndex, arr) => {
                index++;
                return React.cloneElement(child, {
                    id: "inputTableDataColumn" + this.props.dataIndex.toString() + index.toString(),
                    data: data,
                    key: "inputTableDataColumn" + index.toString(),
                    columnIndex: index,
                    changeHandler: this.props.changeHandler,
                    dataIndex: this.props.dataIndex,
                    counterName: this.props.counterName,
                    addRowCalled: this.props.addRowCalled,
                    totalDataItems: this.props.totalDataItems,
                    triggerValidation: this.props.triggerValidation,
                    rowValidationObject: this.props.rowValidationObject
                });
            });
            // add column for "delete" button
            index++;
            if (this.props.dataIndex == 0 && !this.props.noDataIndexThreshold) {  // threshold row does not get the delete row button
                renderedColumns.push(React.createElement(InputTableLabelColumn, {
                    key: "inputTableDataColumn" + index.toString(),
                    text: ""
                }));
            } else {
                if (totalTierRows <= requiredTierRows) { // rows cannot be deleted because they are required now
                    renderedColumns.push(React.createElement(InputTableLabelColumn, {
                        key: "inputTableDataColumn" + index.toString(),
                        text: ""
                    }));
                } else if (!this.props.data['hideDeleteButton']) {
                        renderedColumns.push(React.createElement(InputTableRemoveRowButtonColumn, {
                        key: "inputTableDataColumn" + index.toString(),
                        dataIndex: this.props.dataIndex,
                        removeRowButtonClickHandler: this.props.deleteRowEventHandler
                    }));
                }
            }
            return renderedColumns;
        };
        return (
            <tbody>
                <tr id={id} style={this.rowStyle}>
                    {renderColumns()}
                </tr>
            </tbody>
        );
    }
    // lifecycle event handlers
});

module.exports = InputTableDataRow;



