var React = require("react");
var _ = require("underscore");

/**
 * Component to render a step in the upload dialog.
 */
var UploadDialogStep = React.createClass({
    // lifecycle event handlers
    render () {
        var stepNumberStyle = {
            fontFamily: "verdana",
            fontSize: 14,
            color: "#333",
            fontWeight: "bold",
            border: "solid 2px #333",
            borderRadius: "50%",
            width: 20,
            height: 20,
            textAlign: "center",
            lineHeight: "20px"
        };
        var stepContainerStyle = {
            marginLeft: 20,
            marginBottom: 20,
            fontFamily: "verdana",
            fontSize: 14,
            color: "#333",
            display: "flex"
        };
        var stepChildrenContainerStyle = {
            paddingTop: 4,
            fontWeight: "normal",
            marginLeft: 12,
            fontFamily: "verdana",
            fontSize: 12
        };
        var stepChildrenContainer = () => {
            return (
                <div style={stepChildrenContainerStyle}>
                    {this.props.children}
                </div>
            );
        };
        var id = this.props.id || _.uniqueId("uploadDialogStep_");
        var renderStepNumber = () => {
            return (
                <div style={stepNumberStyle}>{this.props.childIndex + 1}</div>
            );
        };
        if (this.props.childIndex === this.props.totalSteps -1) {
            // last step then the margin bottom is 12px
            stepContainerStyle.marginBottom = 12;
        }
        //adding a class name for adding css in mdlt 
        var stepClass = 'stepClass' + (this.props.childIndex + 1);
        return (
            <div id={id} className={stepClass} style={stepContainerStyle}>
                {renderStepNumber()}
                {stepChildrenContainer()}
            </div>
        );
    }
    // lifecycle event handlers
});

module.exports = UploadDialogStep;


