var React = require("react");
var _ = require("underscore");

/**
 * The Secondary Button is a command button that executes an action. The secondary button is different from the one found in a sidebar, which has a set width. Visually it does not have a border or background. The secondary button is used for the Cancel button, as well as other buttons such as the Add Row and Delete buttons.
 */
var SecondaryButton = React.createClass({
    // custom properties
    secondaryButtonBaseStyle: {
        display: "inline", // use this style so that we get a block element, more explanation below for vertical align
        textAlign: "left",
        fontFamily: "verdana",
        fontSize: 11,
        fontWeight: "normal",
        lineHeight: "normal",
        height: 20,
        border: "none",
        backgroundColor: "transparent",
        outline: "none"
    },
    secondaryButtonStyle () {
        return _.extend(
            {
                cursor: "default",
                color: "#999999"
            },
            this.secondaryButtonBaseStyle
        )
    },
    secondaryButtonEnabledStyle () {
        return _.extend(
            {
                cursor: "pointer",
                color: "#285C7E"
            },
            this.secondaryButtonBaseStyle
        )
    },
    secondaryButtonEnabledHoverStyle () {
        return _.extend(
            {
                cursor: "pointer",
                color: "#2C4F61"
            },
            this.secondaryButtonBaseStyle
        )
    },
    innerLayerStyle: {
        display: "table-cell", // use this style so that we can vertically middle align the content in the button
        verticalAlign: "middle",
        height: 22
    },
    textContainerStyle: {
        // the following two styles are required to make the text-overflow work.
        whiteSpace: "nowrap",
        overflow: "hidden",
        textOverflow: "ellipsis",
        verticalAlign: "bottom"
    },
    // custome properties
    // custom methods
    handleFocus (event) {
        this.setState({
            isFocused: !this.props.noFocus
        });
    },
    handleLostFocus (event) {
        this.setState({
            isFocused: false
        });
    },
    // custom methods
    // lifecycle event handlers
    propTypes: {
        /**
         * Id for the button, if not provided a unique id is added.
         */
        id: React.PropTypes.string,
        /**
         * Handler for the click event of the button.
         */
        clickHandler: React.PropTypes.func,
        /**
         * Text to display on the button.
         */
        text: React.PropTypes.string,
        /**
         * Boolean that enables or disables the button. Defaults to true.
         */
        enabled: React.PropTypes.bool,
        /**
         * CSS class for icon. Example "fa fa-database" if using font-awesome icons.
         */
        icon: React.PropTypes.string,
        /**
         * Determines the focusable order of the element in the navigation.
         */
        tabIndex: React.PropTypes.oneOfType([
            React.PropTypes.string,
            React.PropTypes.number
        ]),
        /**
         * Flag to turn off focus style as it will be distracting under certain circumstances. Default is false.
         */
        noFocus: React.PropTypes.bool,
        /**
         * Renders a data-button-type attribute in the HTML element. Useful for automated testing, but can also be used for
         * specifying intent.
         */
        type: React.PropTypes.string
    },
    /**
     * We need to set the enabled state when the enabled props change
     */
    componentWillReceiveProps (newProps) {
        var enabled = (newProps.enabled == false ? false : true);
        if (enabled != this.state.enabled) {
            this.setState({ enabled: enabled });
        }
    },
    getInitialState () {
        var enabled = (this.props.enabled == false ? false : true);
        return {
            enabled: enabled, // value set by prop because it is the default value
            showHover: false,
            isFocused: false
        };
    },
    render () {
        /**
         * Handler function for MouseEnter event
         * @param {Object} The event object from the MouseEnter event
         */
        var showHover = (event) => {
            if (this.state.enabled == true) {
                this.setState({ showHover: true });
            }
            event.stopPropagation();
        };
        /**
         * Handler function for MouseLeave event
         * @param {Object} The event object from the MouseLeave event
         */
        var hideHover = (event) => {
            if (this.state.enabled == true) {
                this.setState({ showHover: false });
            }
            event.stopPropagation();
        };

        var clickHandler = () => {
            if (this.state.enabled == true) {
                return this.props.clickHandler;
            } else {
                return null;
            }
        };
        var currentStyle = () => {
            var buttonStyle = {};
            if (this.state.enabled == false) {
                buttonStyle = this.secondaryButtonStyle();
            } else {
                if (this.state.showHover == true) {
                    buttonStyle =  this.secondaryButtonEnabledHoverStyle();
                } else {
                    buttonStyle = this.secondaryButtonEnabledStyle();
                }
            }
            if (this.props.overrideStyle) {
                buttonStyle = _.extend(
                    buttonStyle,
                    this.props.overrideStyle
                );
            }
            if (this.props.type == "sidebar") {
                buttonStyle.width = 224;
                this.textContainerStyle.width = 172;
                this.textContainerStyle.maxWidth = 172;
            } else {
                buttonStyle.width = "auto";
                buttonStyle.maxWidth = 200;
                this.textContainerStyle.width = "auto";
                this.textContainerStyle.maxWidth = 148;
            }
            // focus
            if (this.state.enabled == true && this.state.isFocused == true) {
                buttonStyle.border = "solid 1px D1DCE4";
                buttonStyle.borderRadius = 0;
                buttonStyle.boxShadow = "0 0 4px #64ADC2";
                buttonStyle.outline = "none";
            }
            // weight
            if (this.props.type == "sidebar" || this.props.type == "actionButton") {
                buttonStyle.fontWeight = "bold";
            } else {
                buttonStyle.fontWeight = "normal";
            }
            return buttonStyle;
        };
        var iconStyle = {
            color: "#346484",
            fontSize: 12,
            marginRight: 6
        };
        if (this.props.type == "sidebar") {
            iconStyle.fontSize = 14;
            iconStyle.marginLeft = 14;
            iconStyle.marginRight = 12;
        } else if (this.props.type == "actionButton") {
            iconStyle.fontSize = 11;
        }
        var icon = () => {
            if (this.props.icon) {
                if (this.state.enabled == false) {
                    iconStyle.color = "#999999";
                } else {
                    iconStyle.color = "#346484";
                }
                return (
                    <span style={iconStyle} className={this.props.icon}></span>
                );
            } else {
                return null;
            }
        };
        /**
         * Method called when the enter key is pressed. It calls the function assigned to the enterPressHandler prop.
         * The value of the textbox along with the keyPress event is passed to the function.
         */
        var handleEnterPress = (event) => {
            var ENTER_KEY_CODE = 13;
            if (event.keyCode == ENTER_KEY_CODE) {
                if (this.state.enabled == true) {
                    this.props.clickHandler(event);
                    // Need to call this function otherwise the clickHandler is called twice.
                    event.preventDefault();
                }
            }
        };
        var tabIndex = -1;
        if (typeof this.props.tabIndex == "number") {
            if (isNaN(parseInt(this.props.tabIndex)) == false) {
                tabIndex = parseInt(this.props.tabIndex);
            }
        }
        var id = this.props.id || _.uniqueId("secondaryButton_");
        var type = "secondaryButton";
        if (this.props.type && this.props.type.length > 0) {
            type = this.props.type;
        }
        var getDisabledProp = () => {
            if (this.state.enabled == true) {
                return false;
            } else {
                return true;
            }
        };

        return (
            <button id={id} style={currentStyle()} disabled={getDisabledProp()} onClick={clickHandler()} data-button-type={type}
                onMouseEnter={showHover} onMouseLeave={hideHover} tabIndex={tabIndex} onKeyDown={handleEnterPress}
                onFocus={this.handleFocus} onBlur={this.handleLostFocus}>
                <div style={this.innerLayerStyle}>
                    {icon()}<span style={this.textContainerStyle} title={this.props.text} data-component-type='Label'>{this.props.text}</span>
                </div>
            </button>
        );
    }
    // lifecycle event handlers
});

module.exports = SecondaryButton;


