/**
 * A dialog allowing user to input comments for deletion
 */
var React = require("react");
var _ = require("underscore");
var i18n = require("i18n");
var Form = require("./Form");
var FormRow = require("./FormRow");
var TextArea = require("./TextArea");
var SecondaryButton = require("./SecondaryButton");
var PrimaryButton = require("./PrimaryButton");
var PageFooter = require("./PageFooter");

var labels = ["enterDeleteComments", "reason", "delete", "cancel", "maxCharacterText", "requiredText", "deleteCommentsValidationMessage"];

/**
 * Overlay component for showing confirmation dialog.
 */
var DeleteCommentsDialog = React.createClass({
    dialogBackdrop: {
        backgroundColor: "#555",
        position: "fixed",
        top: 0,
        right: 0,
        bottom: 0,
        left: 0,
        zIndex: 1040,
        opacity: 0.8,
        display: "block"
    },
    modal: {
        backgroundColor: "#fff",
        fontSize: "12px",
        width: 610,
        position: "fixed",
        left: "calc(50% - 305px)",
        top: "10%",
        marginLeft: "auto",
        marginRight: "auto",
        zIndex: 1050,    
        border: "1px solid #CFCFCF",
        outline: "none"
    },
    header: {
        padding: "21px 20px",
        lineHeight: "22px",
    },
    headerTitle: {
        fontFamily: "verdana",
        fontWeight: "bold",
        fontSize: 18,
        color: "#333333"
    },
    headerClose: {
        float: "right",
        color: "#CACBCA",
        fontSize: 18,
        top: 2,
        position: "relative",   
        cursor: "pointer",
    },
    body: {
        borderTop: "1px solid #E1E1E1",
        borderBottom: "1px solid #E1E1E1",
        fontFamily: "verdana",
        fontSize: 14,
        color: "#333333",
        paddingRight: 20
    },
    // styles
    // properties
    // properties
    // lifecycle event handlers
    propTypes: {
        /**
         * Toggle to show or hide the overlay. False by default.
         */
        show: React.PropTypes.bool.isRequired,
        /**
         * Title of the Overlay.
         */
        headerText: React.PropTypes.string,
        /**
         * The message in the overlay.
         */
        message: React.PropTypes.string,
        /**
         * The text for the Primary button.
         */
        primaryButtonText: React.PropTypes.string,
        /**
         * The text for the cancel button.
         */
        cancelButtonText: React.PropTypes.string,
        /**
         * The id of the primary button on the confirmation dialog. If not provided a unique id will be used.
         */
        primaryButtonId: React.PropTypes.string,
        /**
         * The id of the cancel button on the confirmation dialog. If not provided a unique id will be used.
         */
        cancelButtonId: React.PropTypes.string,
        /**
         * Handler to be passed into the component. It is called when the confirm button is clicked.
         */
        confirmClickHandler: React.PropTypes.func,
        /**
         * Handler called when the cancel button is clicked.
         */
        cancelClickHandler: React.PropTypes.func
    },
    getInitialState () {
        return {
            comments: null,
            show: false,
            showFormValidationMessage: false,
            commentsValid: false
        };
    },
    componentWillMount() {
        i18n.getAll(this.props.namespace, labels);
    },
    componentWillReceiveProps (newProps) {
        if (typeof newProps.show == "boolean") {
            this.setState({
                comments: null,
                show: newProps.show,
                showFormValidationMessage: false,
                commentsValid: false
            });
        }
    },
    // Function called when the comments textarea value is changed
    changeHandler (comments, event) {
        this.setState({
            comments: comments
        });
    },
	render() {
        var confirmClickHandler = () => {
            if (!this.state.commentsValid) {
                this.setState({
                    showFormValidationMessage: true
                });
                return;
            }
            if (typeof this.props.confirmClickHandler == "function") {
                this.props.confirmClickHandler(this.state.comments);
            }
        };
        var cancelClickHandler = () => {
            this.setState({
                show: false
            }, () => {
                if (typeof this.props.cancelClickHandler == "function") {
                    this.props.cancelClickHandler();
                }
            });
        };
        var descriptionValidator = (value) => {
            var validationMessage = null;
            // required
            if (!value || value.length == 0) {
                validationMessage = labels.requiredText;
            }
            // max length
            if (value && value.length > 1500) { // max 1500 characters
                validationMessage = "1500 " + lables.maxCharacterText;
            }
            this.setState({
                commentsValid: validationMessage == null    // set according to whether a message was generated (indicating a failure)
            });
            return validationMessage;
        };
        var headerText = this.props.headerText || labels.enterDeleteComments;
        var message = this.props.message || "";
        var primaryButtonText = this.props.primaryButtonText || labels.delete;
        var primaryButtonId = this.props.primaryButtonId || _.uniqueId("confirmationDialogPrimaryButton_");
        var cancelButtonId = this.props.cancelButtonId || _.uniqueId("confirmationDialogCancelButton_");
        var id = this.props.id || _.uniqueId("confirmationDialog_");

        if (this.state.show == false) {
            return null;
        } else {
            return (
                <div id={id}>
                    <div style={this.dialogBackdrop}></div>
                    <div style={this.modal}>
                        <div style={this.header}>
                            <i className="fa fa-times" style={this.headerClose}  onClick={cancelClickHandler}></i>
                            <div style={this.headerTitle}>
                                {headerText}
                            </div>
                        </div>
                        <div style={this.body}>
                        <Form type={"dialog"} showValidationMessage={this.state.showFormValidationMessage} formValidationMessage={labels.deleteCommentsValidationMessage}>
                            {message && <div style={{paddingLeft: 20, paddingBottom: 20}}>{message}</div>}
                            <FormRow label={labels.reason}>
                                <TextArea id="comments" maxLength={1500} width={400} height={70} size="medium" value={this.state.comments} required={true} tabIndex={1}
                                    changeHandler={this.changeHandler} validator={descriptionValidator} validationEventHandler={null} autoFocus={true} />
                            </FormRow>
                        </Form>
                        </div>
                        <PageFooter>
                            <PageFooter.RightPane>
                                {this.props.type == 'info' ? null : <SecondaryButton id={cancelButtonId} text={this.props.cancelButtonText || labels.cancel} 
                                    icon="fa fa-times-circle-o" clickHandler={cancelClickHandler} tabIndex={3}/>}
                                <PrimaryButton id={primaryButtonId} inline={true}
                                    text={primaryButtonText} clickHandler={confirmClickHandler} tabIndex={2}/>
                            </PageFooter.RightPane>
                        </PageFooter>
                    </div>
                </div>
            );
        }
    }
    // lifecycle event handlers
});

module.exports = DeleteCommentsDialog;


