var React = require("react");
var TableRow = require("./TableRow");

/**
 * A table row that expands to show child content when clicked.
 */
var TableExpandableRow = React.createClass({
    // custom methods
    expandedRowClickHandler (rowData, rowIndex, clickEvent) {
        this.props.rowClickHandler(rowData, rowIndex, clickEvent);
    },
    setRowColumnCount (count) {
        this.setState({
            rowColumnCount: count
        });
    },
    // custom methods
    // lifecycle event handler
    getInitialState () {
        return {
            rowColumnCount: null,
        };
    },
    render () {
        var renderChildren = () => {
            return React.Children.map(this.props.children, (child, index, arr) => {
                return React.cloneElement(child, {
                    rowData: this.props.rowData,
                    dataIndex: this.props.dataIndex,
                    isExpanded: this.props.rowData.isExpanded,
                    rowClickHandler: this.expandedRowClickHandler,
                    visible: this.props.visible,
                    rowColumnCount: this.state.rowColumnCount,
                    setRowColumnCount: this.setRowColumnCount,
                    conditionalBold: this.props.conditionalBold,
                    rowSelectionEnabled: this.props.rowSelectionEnabled
                });
            });
        };
        if (this.props.rowData && this.props.rowData != "headerOnly") {
            return (
                <tbody>
                    {renderChildren()}
                </tbody>
            );
        } else {
            return null;
        }
    }
    // lifecycle event handler
});

module.exports = TableExpandableRow;


