var React = require("react");
var _ = require("underscore");
require("./Tabs.css");

/**
 * The container for the tabs. Individual tabs can be added declaratively.
 * Each tab can have sections, each section will have rows which will display
 * name-value pairs. A user can also add their own content inside the tab.
 */
var Tabs = React.createClass({
    setCurrentTab (tabId, tabIndex, event) {
        const doSetCurrentTab = (canSetTab) => {
            if (canSetTab) {
                this.setState({
                    selectedTabId: tabId,
                    selectedTabIndex: tabIndex
                }, () => {
                    if (typeof this.props.tabClickHandler == "function") {
                        this.props.tabClickHandler(tabId, tabIndex, event);
                    }
                });
            };
        };
        // If a preAddEditTabEventHandler is found, call to see if we should switch tab
        if (typeof this.props.preAddEditTabEventHandler == "function") {
            this.props.preAddEditTabEventHandler(tabId, this.state.selectedTabIndex, doSetCurrentTab, event);
        // otherwise just switch
        } else {
            doSetCurrentTab(true);
        }

    },
    getInitialState () {
        return {
            selectedTabId: null,
            selectedTabIndex: this.props.initialTabIndex 
        };
    },
    componentWillReceiveProps (nextProps) {
        if (this.state.selectedTabIndex != nextProps.initialTabIndex) {
            this.setState({
                selectedTabIndex: nextProps.initialTabIndex
            });
        };
    },
    propTypes: {
        /**
         * The initial tab that is selected when the component loads. By default the first tab is shown.
         */
        initialTabIndex: React.PropTypes.number,
        /**
         * Function handler called when a tab is clicked. The arguments passed are the tab id, tab index and the click event. 
         */
        tabClickHandler: React.PropTypes.func
    },
    render () {
        var renderChildren = () => {
            return React.Children.map(this.props.children, (child, index, arr) => {
                return React.cloneElement(child, {
                    selectedTabId: this.state.selectedTabId,
                    selectedTabIndex: this.state.selectedTabIndex,
                    tabClickHandler: this.setCurrentTab,
                    rowData: this.props.rowData,
                    rowIndex: this.props.rowIndex || 0
                });
            });
        };
        return (
            <div id="tabs" className="tabs_rootContainer">
                {renderChildren()}
            </div>
        );
    }
});

Tabs.Headers = React.createClass({
    propTypes: {
        /**
         * If set to true, increase the width so the truncation kicks in later
         */
        noTruncate: React.PropTypes.bool
    },
    render () {
        var renderChildren = () => {
            return React.Children.map(this.props.children, (child, index, arr) => {
                return React.cloneElement(child, {
                    selectedTabId: this.props.selectedTabId,
                    selectedTabIndex: this.props.selectedTabIndex,
                    tabClickHandler: this.props.tabClickHandler,
                    noTruncate: this.props.noTruncate,
                    tabIndex: index, // Headers tab has the Header components which are the tabs
                    rowData: this.props.rowData,
                    rowIndex: this.props.rowIndex
                });
            });
        };
        return (
            <div id="headers" className="tabs_headers">
                <div id="indentHeader" className="tabs_indentHeader"></div>
                {renderChildren()}
                <div id="spaceHeader" className="tabs_spaceHeader"></div>
            </div>
        );
    }
});

Tabs.Header = React.createClass({
    getInitialState () {
        return {
            marked: this.props.marked 
        };
    },
    componentWillReceiveProps (nextProps) {
        if (this.state.marked != nextProps.marked) {
            this.setState({
                marked: nextProps.marked
            });
        };
    },
    propTypes: {
        /**
         * The id for the tab. If an id is not provided a computed id is used instead.
         */
        id: React.PropTypes.string,
        /**
         * The title for the tab.
         */
        title: React.PropTypes.string,
        /**
         * Give an indicator to highlight the tab header
         */
        marked: React.PropTypes.bool,
        /**
         * Truncate the header or not
         */
        noTruncate: React.PropTypes.bool
    },
    render () {
        var id = this.props.id || _.uniqueId("tabHeader_");
        var tabClickHandler = (event) => {
            if (typeof this.props.tabClickHandler == "function") {
                this.props.tabClickHandler(id, this.props.tabIndex, event);
            }
        };
        var tabStyle = null;
        if (this.props.tabIndex == this.props.selectedTabIndex) {
            tabStyle = "tabs_headerSelected";
            if (this.props.emptyHeader) {
                tabStyle += " tabs_headerEmpty";
            }
        } else {
            tabStyle = "tabs_header";
            if (id == "addTab") {
                tabStyle += " tabs_addTab";
            } else if (id == "moreTabs") {
                tabStyle += " tabs_moreTabs";
            }
        }
        if (this.state.marked) {
            tabStyle += " tabs_marked";
        }
        let headerTitle = <span title={this.props.title}>{this.props.title}</span>;
        if (id == "addTab") {
            headerTitle = <span className="fa fa-plus-circle"></span>;
        } else if (id == "moreTabs") {
            headerTitle = <span className="fa fa-angle-double-right"></span>;
        };
        return ( 
            <div id={id} ref="title" className={tabStyle} onClick={tabClickHandler}>
                <div className={this.props.noTruncate ? "tabs_noTruncate" : "tabs_truncate"}>
                    {headerTitle}
                </div>
            </div>
        );
    }
});

/**
 * Parent element for the container component of each tab.
 */
Tabs.Containers = React.createClass({
    render () {
        var renderChildren = () => {
            return React.Children.map(this.props.children, (child, index, arr) => {
                var isVisible = false;
                if (index == this.props.selectedTabIndex) {
                    isVisible = true;
                }
                return React.cloneElement(child, {
                    visible: isVisible,
                    tabIndex: index,
                    rowData: this.props.rowData,
                    rowIndex: this.props.rowIndex
                });
            });
        };
        return (
            <div id="containers" className="tabs_containers">
                {renderChildren()}
            </div>
        );
    }
});

Tabs.Container = React.createClass({
    render () {
        var id = this.props.id || _.uniqueId("container_");
        var isVisible = false;
        if (typeof this.props.visible == "boolean") {
            isVisible = this.props.visible;
        }
        if (isVisible == true) {
            var renderChildren = () => {
                return React.Children.map(this.props.children, (child, index, arr) => {
                    return React.cloneElement(child, {
                        tabIndex: this.props.tabIndex,
                        rowData: this.props.rowData,
                        rowIndex: this.props.rowIndex
                    });
                });
            };
            return (
                <div id={id} className="tabs_container">
                    {renderChildren()}
                </div>
            );
        } else {
            return null;
        }
    }
});

Tabs.Sections = React.createClass({
    render () {
        var renderChildren = () => {
            return React.Children.map(this.props.children, (child, index, arr) => {
                return React.cloneElement(child, {
                    tabIndex: this.props.tabIndex,
                    rowData: this.props.rowData,
                    rowIndex: this.props.rowIndex,
                    sectionIndex: index
                });
            });
        };
        return (
            <div>
                {renderChildren()}
            </div>
        ); 
    }
});

Tabs.Section = React.createClass({
    propTypes: {
        /**
         * The section title.
         */
        title: React.PropTypes.string
    },
    render () {
        var renderSectionItem = (sectionItem) => {
            var sectionName = "";
            if (sectionItem && typeof sectionItem.props.name == "string" && sectionItem.props.name.length > 0) {
                sectionName = sectionItem.props.name + ":";
            }
            if (sectionItem) {
                return (
                    <Tabs.SectionItem sectionName={sectionName}>
                        {sectionItem.props.children}
                    </Tabs.SectionItem>
                );
            }
        };
        var renderSectionItems = () => {
            var sectionItems = [];
            var sectionItemsArray = React.Children.toArray(this.props.children);
            var index = 0;
            while (index < sectionItemsArray.length) {
                sectionItems.push(
                    <tr key={"section" + index}>
                        <td className="tabs_sectionItemContainer">
                            {renderSectionItem(sectionItemsArray[index])}
                        </td>
                        <td className="tabs_sectionItemContainer">
                            {renderSectionItem(sectionItemsArray[index + 1])}
                        </td>
                    </tr>
                );
                index = index + 2;
            };
            return (
                <tbody>
                    {sectionItems}
                </tbody>
            );
        };
        var renderSectionHeader = () => {
            if (this.props.title && this.props.title.length > 0) {
                return (
                    <tbody>
                        <tr>
                            <td colSpan={2} className="tabs_sectionTitle">{this.props.title}</td>
                        </tr>
                    </tbody>
                );
            } else {
                return null;
            }
        };
        var renderChildren = () => {
            var id = this.props.id || ("row" + this.props.rowIndex + "Section" + this.props.sectionIndex);
            return (
                <table id={id} className="tabs_sectionContainer">
                    {renderSectionHeader()}
                    {renderSectionItems()}
                </table>
            );
        };
        return (
            <div>
                {renderChildren()}
            </div>
        );
    }
});

Tabs.SectionItem = React.createClass({
    propTypes: {
        /**
         * The label for the section item.
         */
        name: React.PropTypes.string
    },
    render () {
        var renderSectionItemLabel = () => {
            if (this.props.sectionName && this.props.sectionName.length > 0) {
                return (
                    <div className="tabs_sectionItemLabel">{this.props.sectionName}</div>
                );
            } else {
                return null;
            }
        };
        return (
            <div>
                {renderSectionItemLabel()}
                <div className="tabs_sectionItemContent"><div className="tabs_sectionItemContent_div">{this.props.children}</div></div>
            </div>
        );
    }
});

module.exports = Tabs;


