/**
 * Constants used by the stores and the dispatcher to denote the type of
 * action being taken
 */

var keyMirror = require("keymirror");

module.exports = {

    FilterType: keyMirror({
        DROP_DOWN: null,
        CHECK_BOX: null,
        RADIO_BUTTON: null,
        SELECT_BOX: null,
        TEXT_BOX: null
    }),

    SideBarButtonType: keyMirror({
        CREATE: null,
        DOWNLOAD: null,
        UPLOAD: null,
        BUTTON_MENU: null
    }),

    ActionButtonType: keyMirror({
        VIEW: null,
        EDIT: null,
        DELETE: null,
        ADD: null,
        COPY: null,
        DOWNLOAD: null,
        RECALL: null
    }),
    /**
     * Enumerations used in the ListPageBuilder component to set the type of selection.
     */
    SelectType: keyMirror({
        NO_SELECT: null,
        SINGLE_SELECT: null,
        MULTI_SELECT: null
    })

};


