/**
 * Component for the side bar in a list page. This is a container that
 * can contain buttons, filter and search sections
 */
var React = require("react");

var sideBarContainerStyle = {
    display: "table-cell",
    padding: "32px 8px 32px 0px",
    width: 248,
    verticalAlign: "top",
    borderRight: "1px solid #E1E1E1",
    backgroundImage: "linear-gradient(-180deg, #fff 9%, #ebf1f5 100%)",
    backgroundImage: "-moz-linear-gradient(center top , #fff, #ebf1f5) repeat scroll 0% 0% transparent",
    backgroundImage: "-ms-linear-gradient(top, #fff, #ebf1f5)",
    backgroundImage: "-webkit-linear-gradient(top, #fff, #ebf1f5)",
    filter: "progid:DXImageTransform.Microsoft.gradient(GradientType=0,startColorstr=#FFFFFF, endColorstr=#EBF1F5)",
};

// Required due to the sizing of display type 'table-cell' elements
var sideBarSpacerStyle = {
    width: 248
};
    
var SideBar = React.createClass({
    render () {
        var id = this.props.id || "sideBar";
        return (
            <div id={id} style={sideBarContainerStyle} data-component-name='SideBar'>
                <div style={sideBarSpacerStyle}></div>
                {this.props.children}
            </div>
        );
    }
});

module.exports = SideBar;


