/**
 * Sub component in the InputTable to display the column name
 */
var React = require("react");

var InputTableHeadColumn = React.createClass({
    // styles
    columnStyle: {
        fontWeight: "bold",
        fontSize:12,
        color:"#333333",
        verticalAlign:"bottom",
        textAlign:"left",
        whiteSpace:"normal",
        paddingTop:8,
        paddingRight:5,
        paddingBottom:8,
        paddingLeft:5,
        height:20,
        width: 128,
        wordWrap: "break-word"
    },
    // styles
    // custom methods
    // custom methods
    // lifecycle event handlers
    render () {
        if(this.props.width) {
            this.columnStyle.width = this.props.width;
        } else {
            this.columnStyle.width = 128;
        }
        return (
            <th style={this.columnStyle}>
                <span>{this.props.columnName}</span>
            </th>
        );
    }
    // lifecycle event handlers
});

module.exports = InputTableHeadColumn;



