var _ = require("underscore");
var React = require("react");
var PrimaryButton = require("./PrimaryButton");

/**
 * This button is found in the sidebar and is generally the main action listed that will be used in that area. One exception is when there is a highlighted filter in the sidebar that will then take visual prominence over this button.
 *The Primary Button - Sidebar looks very similar to the Primary Button that can be found in forms, modal dialogs, and other areas where there is a major call to action. The notable differences are the set width of the button and the spacing above and below the button when in the sidebar.
 */
var SideBarPrimaryButton = React.createClass({
    // custom properties
    primaryButtonBaseStyle: {
        display: "flex",
        alignItems: "center",
        textAlign: "left",
        fontFamily: "verdana",
        width: 200,
        fontSize: 12,
        lineHeight: "normal",
        fontWeight: "bold",
        textShadow: "0 0 0 rgba(0,0,0,0.3)",
        height: 32,
        borderRadius: 20,
        WebkitBorderRadius: 20,
        boxShadow: "0 1px 1px 1px rgba(0,0,0,0.1)",
        outline: "none"
    },
    primaryButtonStyle () {
        return _.extend(
            {
                cursor: "default",
                color: "#CFCFCF",
                border: "1px solid #D1DCE4",
                background: "linear-gradient(180deg, #FFFFFF, #EBF1F5)"
            },
            this.primaryButtonBaseStyle
        )
    },
    primaryButtonEnabledStyle () {
        return _.extend(
            {
                cursor: "pointer",
                color: "#285C7E",
                border: "1px solid #D1DCE4",
                background: "linear-gradient(180deg, #FFFFFF, #EBF1F5)"
            },
            this.primaryButtonBaseStyle
        )
    },
    primaryButtonEnabledHoverStyle () {
        return _.extend(
            {
                cursor: "pointer",
                color: "#203a4a",
                border: "1px solid #c9c9c9",
                background: "linear-gradient(180deg, #e3e3e3, #cddde6)"
            },
            this.primaryButtonBaseStyle
        )
    },
    innerLayerStyle: {
        display: "flex",
        alignItems: "center",
        height: 20
    },
    textContainerStyle: {
        paddingLeft: 6,
        paddingRight: 16,
        width: 140,
        // the following two styles are required to make the text-overflow work.
        whiteSpace: "nowrap",
        overflow: "hidden",
        textOverflow: "ellipsis",
        display: "inline-block"
    },
    buttonContainerStyle: {
        display: "flex",
        alignItems: "center"
    },
    // custome properties
    // custom methods
    handleFocus (event) {
        this.setState({
            isFocused: true
        });
    },
    handleLostFocus (event) {
        this.setState({
            isFocused: false
        });
    },
    // custom methods
    // lifecycle event handlers
    /**
     * We need to set the enabled state when the enabled props change
     */
    componentWillReceiveProps (newProps) {
        var enabled = (newProps.enabled == false ? false : true);
        if (enabled != this.state.enabled) {
            this.setState({ enabled: enabled });
        }
    },
    getInitialState () {
        var enabled = (this.props.enabled == false ? false : true);
        return {
            enabled: enabled, // value set by prop because it is the default value
            showHover: false,
            isFocused: false
        };
    },
    propTypes: {
        /**
         * Id for the button
         */
        id: React.PropTypes.string,
        /**
         * Text to display on the button
         */
        text: React.PropTypes.string,
        /**
         * Enables or disables the button. Default is true-enabled.
         */
        enabled: React.PropTypes.bool,
        /**
         * The type of button. This decides the type of icon that is used.
         * The only option right now is "create". 
         */
        type: React.PropTypes.oneOf([
            "create"
        ]),
        /**
         * Font-Awesome CSS class for icon. Example "fa fa-car"
         */
        icon: React.PropTypes.string,
        /**
         * Handler for the click event of the button.
         */
        clickHandler: React.PropTypes.func,
        /**
         * Show the help icon and render the children of the button as the help overlay.
         */
        showHelpIcon: React.PropTypes.bool
    },
    render () {
        var clickHandler = () => {
            if (this.state.enabled == true) {
                return this.props.clickHandler;
            } else {
                return null;
            }
        };
        var currentStyle = () => {
            var buttonStyle = {};
            if (this.state.enabled == false) {
                buttonStyle = this.primaryButtonStyle();
            } else {
                if (this.state.showHover == true) {
                    buttonStyle =  this.primaryButtonEnabledHoverStyle();
                } else {
                    buttonStyle = this.primaryButtonEnabledStyle();
                }
            }
            if (this.props.inline == true) {
                buttonStyle.display = "inline-flex";
            }
            if (this.props.overrideStyle) {
                buttonStyle = _.extend(
                    buttonStyle,
                    this.props.overrideStyle
                );
            }
            // focus
            if (this.state.enabled == true && this.state.isFocused == true) {
                buttonStyle.background = "linear-gradient(180deg, #FFFFFF, #EBF1F5)";
                buttonStyle.borderRadius = 100;
                buttonStyle.boxShadow = "0 0 4px #64ADC2";
                buttonStyle.outline = "none";
            }
            return buttonStyle;
        };
        var icon = () => {
            if (this.props.type == "create") {
                return (
                    <span style={{marginLeft:14}} className="fa-stack">
                        <i style={{color: "#346F8F"}} className="fa fa-circle fa-stack-2x"></i>
                        <i style={{color: "#CDE2EC", fontSize:21}} className="fa fa-plus-circle fa-stack-1x"></i>
                    </span>
                );
            } else if (this.props.icon) {
                return (
                    <span style={{marginLeft:16, fontSize: 16}} className={this.props.icon}></span>  
                );
            } else {
                return null;
            }
        };
        /**
         * Function called when the mouse enters the button
         */
        var showHover = (event) => {
            if (this.state.enabled == true) {
                this.setState({ showHover: true });
            }
        };
        /**
         * Function called when the mouse leaves the button
         */
        var hideHover = (event) => {
            if (this.state.enabled == true) {
                this.setState({ showHover: false });
            }
        };
        // used for getting the enabled/disabled state of the button component used by UI automation
        var getComponentState = () => {
            if (this.state.enabled === true) {
                return 'enabled';
            }
            return 'disabled';
        };

        var id = this.props.id || _.uniqueId("sideBarPrimaryButton_");
        /**
         * Function that renders the button.
         */
        var renderButton = () => {
            return (
                <div id={id} data-component-type="Button" data-component-name="SideBarPrimaryButton" style={currentStyle()} onClick={clickHandler()} onMouseEnter={showHover} onMouseLeave={hideHover}
                    onFocus={this.handleFocus} onBlur={this.handleLostFocus} data-component-state={getComponentState()}>
                    <div style={this.innerLayerStyle}>
                        {icon()}<span style={this.textContainerStyle} title={this.props.text} data-component-type='Label'>{this.props.text}</span>
                    </div>
                </div>
            );
        };
        if (this.props.showHelpIcon == true) {
            return (
                <div style={this.buttonContainerStyle}>
                    {renderButton()}
                    {this.props.children}
                </div>  
            );
        } else {
            return renderButton();
        }
    }
    // lifecycle event handlers
});

module.exports = SideBarPrimaryButton;


