/**
 * Component that displays a row of the form. Allows the user to specify a label which is
 * displayed on the left hand side and insert a component which is displayed on the right hand
 * side of a colon
 * @param {String} label The text to display on the left hand side of the row.
 * @param {Boolean} optional When set to true displays the 'optional' text under the label
 * @param {String} description Secondary help text displayed above the component
 */

var React = require("react");
var Label = require("./Label");
var _ = require("underscore");

var FormRow = React.createClass({
    // styles
    containerStyle: {
        width: "100%",
        marginBottom: 24,
        display: "flex", // allows for vertical aligning
        alignItems: "flex-start" // align at the top
    },
    labelContainerStyle: {
        display: "flex",
        justifyContent: "flex-end", // right
        alignItems: "flex-start", // top
        width: 160,
        paddingRight: 16
    },
    descriptionContainerStyle: {
        fontFamily: "Verdana",
        fontSize: 12,
        color: "#333333",
        marginBottom: 8,
        whiteSpace: "pre"
    },
    componentContainerStyle: {
        display: "inline-block",
        textAlign: "left"
    },
    // styles
    // life cycle event handlers
    propTypes: {
        label: React.PropTypes.string,
        description: React.PropTypes.string,
        type: React.PropTypes.string
    },
    render () {
        var getDescription = () => {
            // Use dangerouslySetInnerHTML so we can have breaks <br> in description
            if (this.props.description && this.props.description.length > 0) {
                return (
                    <div style={this.descriptionContainerStyle}>
                        <span dangerouslySetInnerHTML={{__html: this.props.description}}></span>
                    </div>
                );
            } else {
                return null;
            }
        };
        var renderLabel = () => {
        	var labelContainerStyle = Object.assign({}, this.labelContainerStyle);
            // check the type prop to see if the value is dialog then the width is 100px
            if (this.props.type == "dialog") {
                labelContainerStyle.width = 100;
            } else {
                labelContainerStyle.width = 160;
            }
            if (!this.props.description) {
                labelContainerStyle.marginTop = 5;
            } 
            if (this.props.label && this.props.label.length > 0) {
                var labelText = this.props.label + ":";
                return (
                    <div data-component-type="Label" style={labelContainerStyle}>
                        <Label text={labelText} align="right"/>
                    </div>
                );
            } else {
                if(this.props.noLabel === true){
                    return ( <div data-component-type="noLabel" style={labelContainerStyle}></div> );
                } else {
                    return null;
                } 
            }
        };
        let componentContainerStyle = _.extend(this.componentContainerStyle, {
           width: this.props.width
        });
        return (
            <div style={this.containerStyle}>
                {renderLabel()}
                <div style={componentContainerStyle}>
                    {getDescription()}
                    {this.props.children}
                </div>
            </div>
        );
    }
    // life cycle event handlers
});

module.exports = FormRow;



