var React = require("react");
var _ = require("underscore");
var ReactTooltip = require("react-tooltip");
require("./HelpDialog.css");

/**
 * The HelpDialog component is used to display help information as a flyout
 * from a 'i' icon.
 */
var HelpDialog = class HelpDialog extends React.Component {

  constructor(props) {
      super(props);
  }

  render(){
        let id = _.uniqueId("helpDialog_");
        var getToolTipBody = () => {
            if(this.props.children){
                return this.props.children;
            }else{
                return "Please insert the required display info here";
            }
        };

    return(
      <div className="helpDialogContainerStyle">
        <a className="icon">
                    <i data-tip data-for={id} className="fa fa-info-circle"></i>
                </a>
                <ReactTooltip id={id} class="tooltipTheme" effect={this.props.effect} type={this.props.type} place={this.props.place}>
                    {getToolTipBody()}
                </ReactTooltip>
      </div>
    );
  }
};

HelpDialog.propTypes = {
    /**
     * position where the tip has to be shown
     */
    place : React.PropTypes.oneOf([
          "top",
          "bottom",
          "right",
          "left"
      ]),
    /**
     * String that shown at the top left corner
     */
    type : React.PropTypes.oneOf([
          "dark",
          "success",
          "warning",
          "error",
          "info",
          "light"
      ]),
    /**
     * String that shown at the top left corner
     */
    effect : React.PropTypes.oneOf([
          "solid",
          "float"
      ])

};

module.exports = HelpDialog;


