var React = require("react");
var _ = require("underscore");
var SecondaryButton = require("./SecondaryButton");
var PrimaryButton = require("./PrimaryButton");
var CheckBox = require("./CheckBox");
var RadioButton = require("./RadioButton");
var Table = require("./Table");
var TableHeadRow = require("./TableHeadRow");
var TableHeadColumn = require("./TableHeadColumn");
var TableNormalRow = require("./TableNormalRow");
var TableTextColumn = require("./TableTextColumn");
var PageFooter = require("./PageFooter");
var FileUpload = require("./FileUpload");
var UploadDialogStep = require("./UploadDialogStep");
var RadioButtonGroup = require("./RadioButtonGroup");
require("./UploadAssignmentsDialog.css");

/**
 * Dialog used for uploading assignments. 
 */
var UploadAssignmentsDialog = React.createClass({
    // properties
    // properties
    // lifecycle event handlers
    propTypes: {
        /**
         * The id of the primary button on the confirmation dialog. If not provided a unique id will be used.
         */
        primaryButtonId: React.PropTypes.string,
        /**
         * The id of the cancel button on the confirmation dialog. If not provided a unique id will be used.
         */
        cancelButtonId: React.PropTypes.string,
        /**
         * Toggle to show or hide the overlay. False by default.
         */
        show: React.PropTypes.bool.isRequired,
        labels: React.PropTypes.shape({
            /**
             * Label for the first step about browsing and selecting a file for upload.
             */
            browseSelectFileStepLabel: React.PropTypes.string,
            /**
             * Label for the file upload button.
             */
            fileUploadButtonText: React.PropTypes.string,
            /**
             * Label for the file upload placeholder.
             */
            fileUploadPlaceholderText: React.PropTypes.string,
            /**
             * Label for the second step about effective dating parameters handling.
             */
            effectiveDatingParametersStepLabel: React.PropTypes.string,
            /**
             * Label for radio button for create new versions.
             */
            newVersionsRadioButtonText: React.PropTypes.string,
            /**
             * Label for radio button for update existing versions.
             */
            existingVersionsRadioButtonText: React.PropTypes.string,
            /**
             * Label for radio button for create new and update existing versions.
             */
            newExistingVersionsRadioButtonText: React.PropTypes.string,
            /**
             * Label for checkbox for aligning to start period.
             */
            alignToStartPeriodCheckBoxText: React.PropTypes.string,
            /**
             * Label for cancel button.
             */
            cancelButtonText: React.PropTypes.string,
            /**
             * Label for the upload button.
             */
            uploadButtonText: React.PropTypes.string,
            /**
             * Label for the error section title.
             */
            errorSectionTitle: React.PropTypes.string,
            /**
             * Download error log hyperlink text.
             */
            downloadErrorLogText: React.PropTypes.string,
            /**
             * Title of the upload dialog.
             */
            fileUploadTitle: React.PropTypes.string
        }),
        /**
         * This should be set to the error response returned by the upload service
         * from XDM when there is an error uploading the file.
         * The format of the errors array JSON should match the following:
         * [
                {
                    "rowIndex": 2,
                    "fieldName": "T1: QA",
                    "errorMessage": "Required field T1: QA. Enter a value."
                },
                {
                    "rowIndex": 2,
                    "fieldName": "T1: PT",
                    "errorMessage": "Required field T1: PT. Enter a value."
                }
            ]
         */
        errors: React.PropTypes.array,
        /**
         * Value used to set the radio button. Has to be one of CREATE/UPDATE/CREATE_AND_UPDATE.
         */
        effectiveDatingValue: React.PropTypes.string,
        /**
         * Value used to set the checkbox for align to start period. Has to be true|false.
         */
        alignToStartPeriodValue: React.PropTypes.bool,
        /**
         * Assigned function is called when there is a change in the effective dating selection.
         * Default is CREATE. Other values are UPDATE and CREATE_AND_UPDATE.
         * Arguments passed to the function are the value (CREATE/UPDATE/CREATE_AND_UPDATE) and the event object.
         */
        effectiveDatingChangeHandler: React.PropTypes.func,
        /**
         * Assigned function is called when there is a change in the state of the align to start period checkbox.
         * Default is false.
         * Argument passed to the function is a boolean - true or false.
         */
        alignToStartPeriodChangeHandler: React.PropTypes.func,
        /**
         * Handler to be passed into the component. It is called when the upload button is clicked.
         */
        uploadClickHandler: React.PropTypes.func.isRequired,
        /**
         * Handler called when the upload dialog cancel button is clicked.
         */
        cancelClickHandler: React.PropTypes.func,
        /**
         * Function to assign to the prop for downloading the error log file.
         */
        downloadErrorLogClickHandler: React.PropTypes.func.isRequired,
        /**
         * Pass false to hide the checkbox option saying Allow mid period dates and align to period start date
         */
        isCheckboxDisplayed : React.PropTypes.bool,
        /**
         * Pass false to hide effective dating parameters radio options in the upload dialog
         */
        isRadioDisplayed : React.PropTypes.bool,
        /**
         * Pass false to hide create and delete radio options in the upload dialog
         */
        isDeleteAndReCreateOptionEnable : React.PropTypes.bool
        
    },
    getInitialState () {
        var showOverlay = false;
        if (typeof this.props.show == "boolean") {
            showOverlay = this.props.show;
        }
        return {
            show: showOverlay,
            files: null
        };
    },
    componentWillReceiveProps (newProps) {
        if (typeof newProps.show == "boolean") {
            this.setState({
                show: newProps.show
            });
        }
    },
    render () {
        var id = this.props.id || _.uniqueId("uploadAssignmentsDialog_");
        var primaryButtonId = this.props.primaryButtonId || _.uniqueId("confirmationDialogPrimaryButton_");
        var cancelButtonId = this.props.cancelButtonId || _.uniqueId("confirmationDialogCancelButton_");
        var isCheckboxDisplayed = this.props.isCheckboxDisplayed === false ? false : true;
        // var title = this.props.title || "[Title]";
        // var errorSectionTitle = this.props.errorSectionTitle || "[Error Uploading File]";
        // var downloadErrorLogText = this.props.downloadErrorLogText || "[Download error log]";
        // var fileUploadButtonText = this.props.fileUploadButtonText || "[Browse...]";
        // var fileUploadPlaceholderText = this.props.fileUploadPlaceholderText || "[Select A File]";

        var isDeleteAndReCreateOptionEnable = this.props.isDeleteAndReCreateOptionEnable === true ? true : false;
        var renderSteps = () => {
            var totalSteps = React.Children.count(this.props.children);
            return React.Children.map(this.props.children, (child, index, arr) => {
                return React.cloneElement(child, {
                    childIndex: index,
                    totalSteps: totalSteps
                });
            });
        };
        /**
         * Function called when the download error log button is clicked.
         * It will call the passed in prop function 'downloadErrorLogClickHandler'
         */
        var downloadErrorLog = (event) => {
            if (typeof this.props.downloadErrorLogClickHandler == "function") {
                this.props.downloadErrorLogClickHandler();
            }
        };

        var renderErrorInformation = () => {
            if (this.props.errors && this.props.errors.length > 0) {
                return (
                    <div className="uploadAssignmentsDialogErrorSectionContainerStyle">
                        <div style={{width: "100%"}}>
                            <div className="uploadAssignmentsDialogErrorHeadingStyle">{getLabels().errorSectionTitle}</div>
                            <div className="uploadAssignmentsDialogErrorLogFileLinkStyle" onClick={downloadErrorLog}>{getLabels().downloadErrorLogText}</div>
                            <div style={{clear: "both"}}></div>
                        </div>
                        <div className="uploadAssignmentsDialogErrorTableContainerStyle">
                            <Table id="errorTable" data={this.props.errors} showRowBorder={false} rowSelectionEnabled={false}>
                                <TableHeadRow>
                                    <TableHeadColumn columnName="Row Index"></TableHeadColumn>
                                    <TableHeadColumn columnName="Field"></TableHeadColumn>
                                    <TableHeadColumn columnName="Message"></TableHeadColumn>
                                </TableHeadRow>
                                <TableNormalRow>
                                    <TableTextColumn textPropertyName="rowIndex"></TableTextColumn>
                                    <TableTextColumn textPropertyName="fieldName"></TableTextColumn>
                                    <TableTextColumn textPropertyName="errorMessage"></TableTextColumn>
                                </TableNormalRow>
                            </Table>
                        </div>
                    </div>
                );
            } else {
                return null;
            }
        };
        var fileChanged = (files, event) => {
            this.setState({
                files: files
            });
        };
        var getUploadButtonEnabledState = () => {
            if (this.state.files && this.state.files.length > 0) {
                return true;
            } else {
                return false;
            }
        };
        var renderFileInput = () => {
            return (
                <div className="uploadAssignmentsDialogInputFileContainerStyle">
                    <FileUpload id={this.props.id} buttonText={fileUploadButtonText} placeholder={fileUploadPlaceholderText}
                        acceptParams=".csv,.xls,.xlsx,application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
                        changeEventHandler={fileChanged} tabIndex={2}/>
                </div>
            );
        };
        var cancelClickHandler = () => {
            this.setState({
                show: false,
                files: null
            }, () => {
                if (typeof this.props.cancelClickHandler == "function") {
                    this.props.cancelClickHandler();
                }
            });
            event.stopPropagation();
        };
        var uploadClickHandler = () => {
            if (typeof this.props.uploadClickHandler == "function") {
                this.props.uploadClickHandler(this.state.files);
            }
            event.stopPropagation();
        };
        var renderParagraphs = () => {
            return React.Children.map(this.props.children, (child) => {
                if (child) {
                    return React.cloneElement(child, null);
                }
            });
        };
        var getLabels = () => {
            var labels = {
                browseSelectFileStepLabel: "[Browse and select the template file to upload.]",
                fileUploadButtonText: "[Browse...]",
                fileUploadPlaceholderText: "[Select A File]",
                effectiveDatingParametersStepLabel: "[Select the effective dating parameters for upload.]",
                newVersionsRadioButtonText: "[Allow creation of new versions]",
                existingVersionsRadioButtonText: "[Allow updates to existing versions]",
                newExistingVersionsRadioButtonText: "[Allow updates and new creation]",
                alignToStartPeriodCheckBoxText: "[Allow mid period dates and align to period start date]",
                cancelButtonText: "[Cancel]",
                uploadButtonText: "[Upload]",
                errorSectionTitle: "[Error Uploading File]",
                downloadErrorLogText: "[Download error log]",
                fileUploadTitle: "[Upload {{Object}} Assignments]",
                deleteCreateVersionsRadioButtonText: "[Allow deletes to all existing rows and create new rows]",
            };
            if (this.props.labels) {
                if (this.props.labels.browseSelectFileStepLabel) {
                    labels.browseSelectFileStepLabel = this.props.labels.browseSelectFileStepLabel;
                }
                if (this.props.labels.fileUploadButtonText) {
                    labels.fileUploadButtonText = this.props.labels.fileUploadButtonText;
                }
                if (this.props.labels.fileUploadPlaceholderText) {
                    labels.fileUploadPlaceholderText = this.props.labels.fileUploadPlaceholderText;
                }
                if (this.props.labels.effectiveDatingParametersStepLabel) {
                    labels.effectiveDatingParametersStepLabel = this.props.labels.effectiveDatingParametersStepLabel;
                }
                if (this.props.labels.newVersionsRadioButtonText) {
                    labels.newVersionsRadioButtonText = this.props.labels.newVersionsRadioButtonText;
                }
                if (this.props.labels.existingVersionsRadioButtonText) {
                    labels.existingVersionsRadioButtonText = this.props.labels.existingVersionsRadioButtonText;
                }
                if (this.props.labels.newExistingVersionsRadioButtonText) {
                    labels.newExistingVersionsRadioButtonText = this.props.labels.newExistingVersionsRadioButtonText;
                }
                if (this.props.labels.alignToStartPeriodCheckBoxText) {
                    labels.alignToStartPeriodCheckBoxText = this.props.labels.alignToStartPeriodCheckBoxText;
                }
                if (this.props.labels.cancelButtonText) {
                    labels.cancelButtonText = this.props.labels.cancelButtonText;
                }
                if (this.props.labels.uploadButtonText) {
                    labels.uploadButtonText = this.props.labels.uploadButtonText;
                }
                if (this.props.labels.errorSectionTitle) {
                    labels.errorSectionTitle = this.props.labels.errorSectionTitle;
                }
                if (this.props.labels.downloadErrorLogText) {
                    labels.downloadErrorLogText = this.props.labels.downloadErrorLogText;
                }
                if (this.props.labels.fileUploadTitle) {
                    labels.fileUploadTitle = this.props.labels.fileUploadTitle;
                }
                if (this.props.labels.deleteCreateVersionsRadioButtonText) {
                    labels.deleteCreateVersionsRadioButtonText = this.props.labels.deleteCreateVersionsRadioButtonText;
                }
            }
            return labels;
        };
        var getTitle = () => {
            return (
                <div className="uploadAssignmentsDialogHeaderTitle">{getLabels().fileUploadTitle}</div>
            )
        };
        var handleEffectiveDatingChange = (value, event) => {
            if (typeof this.props.effectiveDatingChangeHandler == "function") {
                this.props.effectiveDatingChangeHandler(value, event);
            }
        };
        var handleAlignToStartPeriodChange = (checked) => {
            if (typeof this.props.alignToStartPeriodChangeHandler == "function") {
                this.props.alignToStartPeriodChangeHandler(checked);
            }
        };

        var renderCheckBoxStep = () => {
            if(this.props.isCheckboxDisplayed === false){
                return null;
            }else{
                return (
                    <div style={{marginTop:8}}>
                        <CheckBox id="alignToStartPeriodCheckBox" disabled={false} text={getLabels().alignToStartPeriodCheckBoxText} value="alignToStart" 
                            defaultChecked={this.props.alignToStartPeriodValue || false} tooltip={getLabels().alignToStartPeriodCheckBoxText} 
                            changeHandler={handleAlignToStartPeriodChange}/>
                    </div>
                );
            }
        };
        var renderRadioOption = () => {
            if(isDeleteAndReCreateOptionEnable) {
                return (
                    <RadioButtonGroup id="effectiveDatingParametersRadioButtonGroup" changeHandler={handleEffectiveDatingChange} groupName="effectiveDatingParametersGroup" initialSelectedValue={this.props.effectiveDatingValue || "CREATE"}>
                        <RadioButton id="newVersionsRadioButton" text={getLabels().newVersionsRadioButtonText} value="CREATE" />
                        <RadioButton id="existingVersionsRadioButton" text={getLabels().existingVersionsRadioButtonText} value="UPDATE" />
                        <RadioButton id="newExistingVersionsRadioButton" text={getLabels().newExistingVersionsRadioButtonText} value="CREATE_AND_UPDATE" />
                        <RadioButton id="deleteCreateVersionsRadioButton" text={getLabels().deleteCreateVersionsRadioButtonText} value="DELETE_AND_CREATE" />
                    </RadioButtonGroup>
                );
            } else {
                return (
                    <RadioButtonGroup id="effectiveDatingParametersRadioButtonGroup" changeHandler={handleEffectiveDatingChange} groupName="effectiveDatingParametersGroup" initialSelectedValue={this.props.effectiveDatingValue || "CREATE"}>
                        <RadioButton id="newVersionsRadioButton" text={getLabels().newVersionsRadioButtonText} value="CREATE" />
                        <RadioButton id="existingVersionsRadioButton" text={getLabels().existingVersionsRadioButtonText} value="UPDATE" />
                        <RadioButton id="newExistingVersionsRadioButton" text={getLabels().newExistingVersionsRadioButtonText} value="CREATE_AND_UPDATE" />
                    </RadioButtonGroup>
                );
            }
        };
        var renderSteps = () => {
            if(this.props.isRadioDisplayed === false) {
                return (
                    <UploadAssignmentsDialog.Steps>
                        <UploadDialogStep>{getLabels().browseSelectFileStepLabel}
                            <div style={{marginTop:8}}>
                                <FileUpload id="myFileUpload" buttonText={getLabels().fileUploadButtonText} placeholder={getLabels().fileUploadPlaceholderText}
                                        acceptParams=".csv,.xls,.xlsx,application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
                                        changeEventHandler={fileChanged}/>
                            </div>
                        </UploadDialogStep>
                    </UploadAssignmentsDialog.Steps>
                );
            } else {
                return (
                    <UploadAssignmentsDialog.Steps>
                        <UploadDialogStep>{getLabels().browseSelectFileStepLabel}
                            <div style={{marginTop:8}}>
                                <FileUpload id="myFileUpload" buttonText={getLabels().fileUploadButtonText} placeholder={getLabels().fileUploadPlaceholderText}
                                        acceptParams=".csv,.xls,.xlsx,application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
                                        changeEventHandler={fileChanged}/>
                            </div>
                        </UploadDialogStep>
                        <UploadDialogStep>{getLabels().effectiveDatingParametersStepLabel}
                            <div style={{marginTop:8}}>
                                {renderRadioOption()}
                            </div>
                            {renderCheckBoxStep()}
                        </UploadDialogStep>
                    </UploadAssignmentsDialog.Steps>
                );
            }
        };
        if (this.state.show == false) {
            return null;
        } else {
            return (
                <div id={id}>
                    <div className="uploadAssignmentsDialogDialogBackdrop"></div>
                    <div className="uploadAssignmentsDialogModal">
                        <div className="uploadAssignmentsDialogHeader">
                            <i className="fa fa-times uploadAssignmentsDialogHeaderClose"  onClick={cancelClickHandler}></i>
                            {getTitle()}
                        </div>
                        <div className="uploadAssignmentsDialogBody">
                            {renderParagraphs()}
                            {renderSteps()}
                            {renderErrorInformation()}
                        </div>
                        <PageFooter type="dialog">
                            <PageFooter.RightPane>
                                <SecondaryButton id={cancelButtonId} text={getLabels().cancelButtonText} 
                                    icon="fa fa-times-circle-o" clickHandler={cancelClickHandler}/>
                                <PrimaryButton id={primaryButtonId} inline={true} enabled={getUploadButtonEnabledState()}
                                    text={getLabels().uploadButtonText} clickHandler={uploadClickHandler}/>
                            </PageFooter.RightPane>
                        </PageFooter>
                    </div>
                </div>
            );
        }
    }
    // lifecycle event handlers
});

UploadAssignmentsDialog.Paragraph = React.createClass({
    render () {
        return (
            <div className="uploadAssignmentsDialogParagraphStyle">   
                {this.props.children}
            </div>
        );
    }
});

UploadAssignmentsDialog.Steps = React.createClass({
    render () {
        var renderSteps = () => {
            var totalSteps = React.Children.count(this.props.children);
            return React.Children.map(this.props.children, (child, index, arr) => {
                return React.cloneElement(child, {
                    childIndex: index,
                    totalSteps: totalSteps
                });
            });
        };
        return (
            <div>
                {renderSteps()}
            </div>
        );
    }
});

module.exports = UploadAssignmentsDialog;


