var React = require("react");
var _ = require("underscore");
var SecondaryButton = require("./SecondaryButton");

var AuditTableStyles = (function () {
    var _auditTable = {
    	titleStyle : {
    		color : '#333333',
    		fontSize:'12px',
        fontFamily: 'Verdana',
    		fontWeight:'bold',
        position: 'absolute',
        left: '35px'
    	},
        tableStyle: {
          width: '100%',
	        border: '1px solid #64ADC2',
	        borderCollapse: 'collapse',
            tableLayout: 'fixed' 
        },
        auditHeaderRowStyle : {
			height : '36px',
			backgroundColor : '#F0F5F8',
			borderBottom : '1px solid #64ADC2',
            textAlign: "center"
		},
		auditHeaderRowCellStyle : {
			minWidth: '180px',
			borderRight : '1px dashed #64ADC2',
            fontFamily: 'Verdana',
            fontWeight: 'bold',
            fontSize: '11px',
            verticalAlign: 'middle'
		},
		auditRowCellStyle : {
	    	borderRight : '1px dashed #64ADC2',
	    	padding: '0px 12px 0px 12px',
            fontFamily: 'Verdana',
            fontSize: '12px',
            verticalAlign: 'middle',
            wordWrap:'break-word'
    	},
    	auditOddRowStyle : {
	    	height : '36px'
	    },
	    auditEvenRowStyle : {
	    	height : '36px',
	    	backgroundColor : '#FAFAFA'
	    }
    };
    return _auditTable;
})();

var AuditTable = class AuditTable extends React.Component {

	constructor(props) {
	    super(props);
	    this.clickHandler = this.clickHandler.bind(this);
	}

    //click handler when the download button is clicked
    clickHandler (event) {
        if (typeof this.props.downloadClickHandler == "function") {
            this.props.downloadClickHandler();
        }
        event.stopPropagation();
    }

	render(){
		var id = this.props.id || _.uniqueId("Audittable_");
        var data = this.props.data || [];
        let maxRows = this.props.maxrows || 20;
        data = data.slice(0, maxRows);
        var clonedData =[];
        if (data.length > 0) {
            data.forEach((dataItem, index, arr) => {
                var clonedItem = _.clone(dataItem);
                clonedData.push(clonedItem);
            });
        }
        // push a fake record to the data if it is empty. This will allow us to render the header
        // if there is no data, we will ignore this entry for data rows.
        if (clonedData.length == 0) {
            clonedData.push("headerOnly");
        }

        var getRows = () => {
        	return clonedData.map((item, index, arr) => {
                return React.Children.map(this.props.children, (rowTemplate) => {
                    if (rowTemplate) {
                        return React.cloneElement(rowTemplate, {
                            rowData: item,
                            key: index,
                            dataIndex: index,
                            itemId: item.id,
                            tableId: id
                      });
                    }
                });
            });

        };

		return(
			<div>
				<AuditTable.MainHeading clickHandler = {this.clickHandler} heading = {this.props.heading} downloadLabel = {this.props.downloadLabel}/>
				<table id={id} style={AuditTableStyles.tableStyle} cellSpacing="0">
                	{getRows()}
            	</table>
			</div>
		);
	}
};

AuditTable.propTypes = {
    /**
     * Assign a React Component to this prop if you want to have control over the rendering of the cells in the column.
     * The React component is passed in the textPropertyName and rowData as props.
     * Use these props to change the styling of the cells in the column.
     */
    customCellRenderer: React.PropTypes.object,
    /**
     * Shown at the top left corner
     */
    heading: React.PropTypes.string,
    /**
     * Gets invoked when the download button is clicked
     */
    downloadClickHandler : React.PropTypes.func,
    /**
     * Data for rendering input to the audit table
     */
    data : React.PropTypes.array,
    /**
     * maximum no of rows that should be displayed.
     */
    maxrows : React.PropTypes.number,
    /**
     * Shown at the top right corner
     */
    downloadLabel : React.PropTypes.string
};

AuditTable.MainHeading = class MainHeading extends React.Component{
	render(){
        let stickyStyle;
        stickyStyle = {
            position: 'absolute', 
            right: 35
        }
        if ((this.props.heading && this.props.heading.length > 0) || (this.props.downloadLabel && this.props.downloadLabel.length > 0)) {
            return(
                <div style={{paddingBottom:30}}>
                    <span style={AuditTableStyles.titleStyle}>{this.props.heading}</span>
                        <span style={stickyStyle}>
                            <SecondaryButton name={'downloadButton'} id={'id'} icon={'fa fa-download'} text={this.props.downloadLabel}
                            type="actionButton" clickHandler={this.props.clickHandler}/>
                        </span>
                </div>
            );
        } else {
            return null;
        }
	}
}

AuditTable.HeadRow = class Header extends React.Component{
    render () {
        var getHeadColumns = () => {
            return React.Children.map(this.props.children, (headColumnTemplate) => {
                if (headColumnTemplate) {
                    return React.cloneElement(headColumnTemplate, {
                        tableId: this.props.tableId
                    });
                }
            });
        };
        var getHeadRow = () => {
            if (this.props.dataIndex == 0) {
                return (
                    <thead>
                        <tr style={AuditTableStyles.auditHeaderRowStyle}>
                            {getHeadColumns()}
                        </tr>
                    </thead>
                );
            } else {
                return null;
            }
        };
        return getHeadRow();
    }
}

AuditTable.HeaderColumn = class HeaderColumn extends React.Component{
    render () {
        var getColumnHeader = () => {
            return (
                <th style={AuditTableStyles.auditHeaderRowCellStyle}>
                    <span>{this.props.columnName}</span>
                </th>
            );
        };
        return getColumnHeader();
    }
}


