AuditTable.Row = class Row extends React.Component{
	render () {
		let id = this.props.id || _.uniqueId("tableRow_");
		let handleChildren = () => {
            return React.Children.map(this.props.children, (child) => {
                if (child) {
                    return React.cloneElement(child, {
                        rowData: this.props.rowData,
                        dataIndex : this.props.dataIndex
                    });
                }
            });
        };

        let oddEvenStyle = this.props.dataIndex % 2 === 0 ? AuditTableStyles.auditOddRowStyle : AuditTableStyles.auditEvenRowStyle;

        let getRow = () => {
            return (
                <tr id={id} style={oddEvenStyle}>
                    {handleChildren()}
                </tr>
            );
        };
		return getRow();
	}
};

AuditTable.Cell = class Cell extends React.Component{
    constructor (props) {
        super(props);
        this.getContent = this.getContent.bind(this);
    }

    render () {
    	var text = "";
    	var propName = this.props.textPropertyName;
    	if(propName && typeof propName === 'string' && this.props.rowData){
    		text = this.props.rowData[propName];
        }
        return (
            <td title={this.props.title} style={AuditTableStyles.auditRowCellStyle}>
                {this.getContent(text)}
            </td>
        );    
    }

    /**
     * Function that returns the custom renderer or the plain text.
     * @param {string} text The text for the cell. 
     */
    getContent (text) {
        if (this.props.customCellRenderer) {
            return (
                React.cloneElement(this.props.customCellRenderer, {
                    textPropertyName: this.props.textPropertyName,
                    rowData: this.props.rowData
                })
            );
        } else {
            return text;
        }
    }
};
module.exports = AuditTable;



