/**
 * Container component for filters in the side bar. This same component can also be used as a search container
 * @param {String} id The id for the component
 * @param {String} title The title for the container
 * @param {Func} clearHandler Shows a clickable "Clear" link and calls the callback when clicked. It is totally up to the caller to determine what to do with that.
 * @param {String} clearText The text to show for the "Clear" link
 * @param {Array} children The items in the container
 */
var React = require("react");
var _ = require("underscore");

var SideBarFilterContainer = React.createClass({
    // styles
    sideBarFilterContainerStyle: {
        verticalAlign: "top",
        paddingLeft: 20,
        paddingBottom: 20,
        paddingRight: 16,
        marginBottom: 16,
        background: "#FFFFFF",
        border: "1px solid #E1E1E1",
        borderLeft: 0,
        borderTopRightRadius: 4,
        borderBottomRightRadius: 4,
        color: "#285c7e",
        fontSize: 12,
        fontWeight: "normal"
    },
    sideBarFilterContainerTitleStyle: {
        fontSize: 14,
        fontWeight: "bold",
        color: "#333333",
        marginTop: 20,
        marginBottom: 12,
        fontFamily: "verdana"
    },
    // styles
    render() {
        var id = this.props.id || _.uniqueId("sideBarFilterContainer_");
        var getTitle = () => {
            if (this.props.title) {
                return (
                	<div style={this.sideBarFilterContainerTitleStyle} >
                    <span data-component-name='SideBarFilterContainer'
                        data-component-type='Label'>{this.props.title}</span>
                    {typeof this.props.clearHandler == "function" &&
	                    <span onClick={this.props.clearHandler} style={{float: "right", fontSize: 13, color: "rgb(40, 92, 126)", lineHeight: "18px", cursor: "pointer"}}>{this.props.clearText}</span>}
                    </div>
                );
            }  
        };
        return (
            <div id={id} style={this.sideBarFilterContainerStyle} data-component-name='SideBarFilterContainer'
                data-component-type='Container'>
                {getTitle()}
                {this.props.children}
            </div>
        );
    }
});

module.exports = SideBarFilterContainer;


