/**
 * Component for displaying the header for the list page and form page
 * Contains the title to the left and a container for buttons to the right
 * The buttons are used on overlays
 */
var React = require("react");
var _ = require("underscore");

var WorkflowStep = React.createClass({
    // styles
    workflowStepStyle: {
        //padding: "0px 20px 0px 20px",
        // backgroundColor: "#eef3f6",
        border: "1px solid #E7E7E7",
        boxShadow: "0 0 2px 0 rgba(0,0,0,0.2)",
        width: "740px"
    },
    titleStyle: {
        fontSize: 14,
        fontWeight: "bold",
        color: "#333333",
        backgroundColor: "#EEF3F6",
        display: "flex",
        alignItems: "center",
        height: 39,
        paddingLeft: "10px",
        borderBottom: '1px solid #569241'
    },
    contentStyle: {
        display: "block",
        padding: "12px 20px 12px 20px",
        backgroundColor: "#FFFFFF"
    },
    clearStyle: {
        float: "clear"
    },
    // styles
    render () {
        var id = this.props.id || _.uniqueId("workflowStep_");
        return (
            <div id={id} style={this.workflowStepStyle}>
                <div style={this.titleStyle}>{this.props.title}</div>
                <div style={this.contentStyle}>{this.props.children}</div>
                <div style={this.clearStyle}></div>
            </div>
        );
    }
});

module.exports = WorkflowStep;



