/**
 * Button that denotes a tertiary action. Visually it does not have a border or background
 * @param {String} id Id for the button
 * @param {Function} clickHandler Handler for the click event of the button
 * @param {String} text Text to display on the button
 * @param {bool} active Boolean that enables or disables the button
 * @param {String} icon CSS class for icon. Example "fa fa-database" if using font-awesome
 */
var React = require("react");
var _ = require("underscore");

var TertiaryMenuButton = React.createClass({
    // custom properties
    tertiaryMenuButtonBaseStyle: {
        display: "inline", // use this style so that we get a block element, more explanation below for vertical align
        textAlign: "left",
        fontSize: 14,
        lineHeight: "normal",
        height: 20,
        paddingBottom: "5px",
        paddingLeft: "20px",
        paddingRight: "20px"
    },
    tertiaryMenuButtonActiveStyle () {
        return _.extend(
            {
                cursor: "pointer",
                color: "#333333"
            },
            this.tertiaryMenuButtonBaseStyle
        )
    },
    tertiaryMenuButtonInActiveStyle () {
        return _.extend(
            {
                cursor: "pointer",
                color: "#346F8F"
                // color: "#285C7E",
            },
            this.tertiaryMenuButtonBaseStyle
        )
    },

    innerLayerStyle: {
        display: "table-cell", // use this style so that we can vertically middle align the content in the button
        verticalAlign: "middle",
    },
    textContainerStyle: {
        paddingLeft: 3,
        paddingRight: 3,
        position: "relative",
        // borderBottom: "2px solid rgb(247, 144, 30)",
        paddingBottom: 3
    },
    // custome properties
    // custom methods
    // custom methods
    // lifecycle event handlers
    /**
     * We need to set the active state when the active props change
     */
    componentWillReceiveProps (newProps) {
        var active = (newProps.active == false ? false : true);
        if (active != this.state.active) {
            this.setState({ active: active });
        }
    },
    getInitialState () {
        var active = (this.props.active == false ? false : true);
        return {
            active: active, // value set by prop because it is the default value
            showHover: false
        };
    },
    render () {
        /**
         * Handler function for MouseEnter event
         * @param {Object} The event object from the MouseEnter event
         */
        var showHover = (event) => {
            if (this.state.active == true) {
                this.setState({ showHover: true });
            }
            event.stopPropagation();
        };
        /**
         * Handler function for MouseLeave event
         * @param {Object} The event object from the MouseLeave event
         */
        var hideHover = (event) => {
            if (this.state.active == true) {
                this.setState({ showHover: false });
            }
            event.stopPropagation();
        };

        var clickHandler = () => {
          //this.setState({ active: true });
          return this.props.clickHandler;
        };
        var currentStyle = () => {
            var buttonStyle = this.tertiaryMenuButtonActiveStyle(); //this.tertiaryMenuButtonActiveStyle();
            if (this.state.active == false) {
                buttonStyle = this.tertiaryMenuButtonInActiveStyle();
                this.textContainerStyle.borderBottom = "none";

                if (this.state.showHover == true) {
                    buttonStyle.color = "#2C4F61"; // =  this.tertiaryMenuButtonActiveHoverInActiveStyle();
                } else if (this.state.showHover == false) {
                    buttonStyle.color = "#346F8F";
                    // buttonStyle = this.tertiaryMenuButtonInActiveHoverInActiveStyle();
                }
            } else {
                this.textContainerStyle.borderBottom = "2px solid #F7901E";
            }
            // if (this.state.showHover == true) {
            //     buttonStyle.color = "#2C4F61"; // =  this.tertiaryMenuButtonActiveHoverInActiveStyle();
            // } else if (this.state.showHover == false) {
            //     buttonStyle.color = "#346F8F";
            //     // buttonStyle = this.tertiaryMenuButtonInActiveHoverInActiveStyle();
            // }
            if (this.props.overrideStyle) {
                buttonStyle = _.extend(
                    buttonStyle,
                    this.props.overrideStyle
                );
            }
            return buttonStyle;
        };
        var icon = () => {
            if (this.props.icon) {
                return (
                    <span style={{marginLeft:10}} className={this.props.icon}></span>
                );
            } else {
                return null;
            }
        };
        var id = this.props.id || _.uniqueId("tertiaryMenuButton_");
        return (
            <div id={id} style={currentStyle()} onClick={clickHandler()} onMouseEnter={showHover} onMouseLeave={hideHover}>
                <div style={this.innerLayerStyle}>
                    {icon()}<span style={this.textContainerStyle}>{this.props.text}</span>
                </div>
            </div>
        );
    }
    // lifecycle event handlers
});

module.exports = TertiaryMenuButton;


