// Sample column component that shows how to add custom HTML to a column
// in the Table component
var React = require("react");
var AbstractTableColumn = require("./AbstractTableColumn");

var TableContainerColumn = React.createClass({
    render () {
        var rowData = this.props.rowData;
        var content = (<table>
                        <tr>
                            <td>
                                <img style={{width:100, height:100}} src={rowData.pic}/>
                            </td>
                            <td>
                                <span>{rowData.fname + " " + rowData.lname}</span>
                            </td>
                        </tr>
                    </table>);
        return (
            React.cloneElement(AbstractTableColumn, this.props, content)
        );
    }
});

module.exports = TableContainerColumn;


