/**
 * Container for the form elements
 */

var React = require("react");
var _ = require("underscore");
var FormStyles = require("./ComponentStyles").form;

var Form = React.createClass({
    // lifecycle event handlers
    render () {
        var id = this.props.id || _.unique("form_");
        var errorMessage = this.props.formValidationMessage || "[Error Message Here]";
        var toggleValidationErrorMessage = () => {
            if (typeof this.props.showValidationMessage == "boolean") {
                if (this.props.showValidationMessage == true) {
                    return (
                        <div style={FormStyles.errorMessageContainerStyle}>
                            <div style={FormStyles.errorMessageWrapperStyle}>
                                <span data-component-type="PageValidationMessage" className="fa fa-exclamation-circle" style={FormStyles.errorMessageIconStyle}></span>
                                <span style={FormStyles.errorMessageStyle} dangerouslySetInnerHTML={{__html: errorMessage}}></span>
                            </div>
                        </div>
                    );
                } else {
                    return null;
                }
            }
        };
        if (this.props.type == "dialog") {
            FormStyles.containerStyle.paddingBottom = 8;
        } else {
            FormStyles.containerStyle.paddingBottom = 32;
        }
        return (
            <div id={id} style={FormStyles.containerStyle}>
                {toggleValidationErrorMessage()}
                {this.props.children}
            </div>              
        );
    }
    // lifecycle event handlers
});

module.exports = Form;


