var React = require("react");

/**
 * Component that renders a standard radio button
 */
var RadioButton = React.createClass({
    // styles
    inputStyle: {
        marginTop: "auto",
        marginBottom: "auto",
        marginRight: 8,
        height: 12,
        width: 12,
        verticalAlign: "middle",
        outline: 'none'
    },
    enabledLabelStyle: {
        fontWeight:"normal",
        cursor: "pointer",
        fontFamily: "verdana",
        fontSize: 12,
        display: "inline"
    },
    disabledLabelStyle: {
        fontWeight:"normal",
        cursor: "not-allowed",
        fontFamily: "verdana",
        fontSize: 12,
        display: "inline"
    },
    // styles
    // custom methods
    handleChange (event) {
        this.setState({
            isChecked: (event.target.checked == true ? true : null)
        });
        if (typeof this.props.changeHandler == "function") {
            this.props.changeHandler(event.target.checked, this.props.value, event);
        }
    },
    // custom methods
    // lifecycle event handlers
    componentWillReceiveProps (newProps) {
        this.setState({ isChecked: newProps.checked });
    },
    getInitialState () {
        var isChecked = false;
        if (typeof this.props.checked == "boolean") {
            isChecked = this.props.checked;
        }
        return {
            isChecked: isChecked
        };
    },
    propTypes: {
        /**
         * Text to display next to the radio button.
         */
        text: React.PropTypes.string,
        /**
         * ID for the component.
         */
        id: React.PropTypes.string,
        /**
         * Property to enable or disable the radio button.
         */
        disabled: React.PropTypes.bool,
        /**
         * The value associated with the radio button.
         */
        value: React.PropTypes.string,
        /**
         * Handler for the change event. The arguments passed into the handlers
         * are a boolean indicating whether the radio button was selected, a string
         * that represents the value of the radio button and the event object.
         */
        changeHandler: React.PropTypes.func,
        /**
         * Tooltip to show on the radio button.
         */
        tooltip: React.PropTypes.string,
        /**
         * Determines the focusable order of the element in the navigation.
         */
        tabIndex: React.PropTypes.number
    },
    render () {
        var id = this.props.id || _.uniqueId("radioButton_");

        var style = {
            display: "block",
            paddingBottom: 4
        };

        if (this.props.type == "inputTableRadioButtonColumn") {
            style = {
                marginRight: 28,
                display: "block",
                paddingBottom: 4,
                paddingTop: 5
            };
        } else {
            style = {
                display: "block",
                paddingBottom: 4
            };
        }
        var labelStyle = () => {
            if (this.props.disabled == true) {
                return this.disabledLabelStyle;
            }
            return this.enabledLabelStyle;
        };
        var addLabel = () => {
            if (this.props.text != null && this.props.text != undefined && this.props.text.length > 0) {
                return (
                    <label style={labelStyle()} title={this.props.tooltip} htmlFor={id}>{this.props.text}</label>
                );
            } else {
                return null;
            }
        };
        var tabIndex = -1;
        if (typeof this.props.tabIndex == "number") {
            if (isNaN(parseInt(this.props.tabIndex)) == false) {
                tabIndex = parseInt(this.props.tabIndex);
            }
        }
        return (
          <span style={this.props.layout == 'horizontal' ? {marginRight: 28} : {display: "block", paddingBottom: 4}}>
                <input id={id} style={this.inputStyle} tabIndex={tabIndex} name={this.props.groupName} type="radio" disabled={this.props.disabled} title={this.props.tooltip}
                    value={this.props.value} checked={this.state.isChecked} onChange={this.handleChange}/>{addLabel()}
            </span>
        );
    }
    // lifecycle event handlers
});

module.exports = RadioButton;



